// Clients widget
.clients {
    padding-bottom: $space-quadruple;

    .clients__heading {
        font-weight: $font-weight-bold;
    }

    .clients__grid {
        align-items: center;
        display: flex;
        flex-wrap: wrap;
        margin-top: $space;
    }

    .clients__grid-item {
        float: left;
        margin: 0;
        padding: $space-double 0;
        width: percentage(1 / 2);

        @include respond-min($large-width) {
            padding: $space-double $space;
            width: percentage(1 / 3);
        }

        .js & {
            opacity: var(--opacity);
            transition: opacity $transition-speed-slow $transition-cubic-bezier;
            transition-delay: .1s;
            will-change: opacity;

            @for $i from 1 through 9 {
                &:nth-child(#{$i}) {
                    transition-delay: #{$i / 10}s;
                }
            }
        }
    }

    .clients__grid-img-link {
        display: block;
        padding: 0;
        transition: opacity $transition-speed $transition-cubic-bezier;

        &::after {
            content: none;
        }

        &:hover {
            opacity: .6;
        }
    }

    .clients__grid-img {
        margin: 0 auto;
        max-width: percentage(3 / 4);

        @include respond-min($large-width) {
            max-width: 100%;
        }
    }
}
