.button--primary,
%button--primary {
    transition: border-color $transition-speed-fast $transition-cubic-bezier,
                color $transition-speed-fast $transition-cubic-bezier;

    &,
    &:visited {
        border-color: $color-primary;
        color: $color-primary;
    }

    &:hover,
    &:focus {
        border-color: $color-secondary;
        color: $color-white;
    }

    &:active {
        border-color: $color-link-border-active;
    }
}
