// Navigation widget
.widget--navigation {
    background-color: $color-white;
    padding-top: $space-triple;

    .nav__heading {
        @extend %gamma;

        color: $color-primary;

        @include respond-min($large-width) {
            display: inline-block;
            float: left;
            padding-right: $space-double;
            width: 50%;
        }
    }
}

.list--navigation {
    margin-top: $space;

    @include respond-min($large-width) {
        display: inline-block;
        float: left;
        margin-top: $space-half;
        width: 50%;
    }

    .list__item {
        @include respond-min($small-width) {
            display: inline-block;
            float: left;
            width: 50%;
        }
    }

    .list__link {
        color: $color-primary;
        letter-spacing: $letter-spacing-x-large;
    }
}
