%list--slim,
.list--slim {
    .list__item {
        margin-top: $space-two-thirds;
    }

    .list__link {
        &::after {
            height: $border-width-small;
        }

        &:hover,
        &:active {
            &::after {
                height: calc(50% + (#{$space-twelfth} / 2));
            }
        }
    }
}
