.project-details {
    .list__heading {
        display: inline-block;
        min-width: $space-quadruple;
        padding-right: $space;

        @include respond-min($large-width) {
            min-width: $space-quadruple + $space-double;
        }
    }

    .list__text {
        display: inline-block;
        text-transform: uppercase;

        .list__item {
            margin-top: 0;
        }
    }

    .list__item {
        display: flex;
        font-weight: $font-weight-semi-bold;
    }

    .list__link {
        @include resetLinks();

        text-transform: uppercase;
    }
}
