%list--categories,
.list--categories {
    @extend %list--inline;

    text-align: center;

    .no-js & {
        display: none;
    }

    .list__link {
        @extend %link;
        @include resetLinks();

        color: $color-primary;

        &::after {
            height: 0;
        }

        &:hover,
        &:focus,
        &:active,
        &.is-active {
            font-weight: $font-weight-semi-bold;

            &::after {
                height: $border-width-large;
            }
        }
    }
}
