.button--success,
%button--success {
    &,
    &:visited {
        background-color: $color-success;
        border-color: darken($color-success, 10%);
        color: $color-white;
    }

    &:hover,
    &:active,
    &:focus {
        background-color: lighten($color-success, 5%);
    }

    &::after {
        content: none;
    }
}
