.blockquote {
    background-color: $color-secondary;
    border-left: 0;
    font-style: normal;
    letter-spacing: normal;
    margin-top: 0;
    max-width: 100%;
    padding: $space-double 0;
    position: relative;

    @include respond-min($mid-width) {
        padding: ($space-quadruple - $space-third) 0;
    }

    .inner-container-large {
        padding: $space-triple 0;

        @include respond-min($mid-width) {
            padding: 0 $space-triple;
            position: relative;
        }
    }

    .blockquote__heading {
        font-family: $font-family-heading;
        letter-spacing: $letter-spacing-mid;
        margin-top: 0;
        text-transform: none;
    }

    .blockquote__meta {
        font-weight: $font-weight-semi-bold;
        margin: $space-double auto 0;
        text-align: center;

        @include respond-min($mid-width) {
            max-width: percentage(2 / 3);
            padding-left: $space-triple;
            position: relative;
            text-align: left;

            &::before {
                background-color: $color-white;
                content: '';
                display: block;
                height: $border-width-mid;
                left: 0;
                margin-right: $space;
                position: absolute;
                top: $space-two-thirds;
                width: $space-double;
            }
        }
    }

    .icon {
        left: 0;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);

        @include respond-max($mid-width) {
            left: $space;
            top: 0;
            transform: none;
        }
    }

    .icon-close-quote {
        left: auto;
        right: 0;

        @include respond-max($mid-width) {
            bottom: $space-half;
            left: auto;
            right: $space;
            top: auto;
        }
    }

    .fade-in-delay {
        transform: translateY(var(--translateY));
        transition: transform $transition-speed-fast $transition-cubic-bezier;
        will-change: transform;
    }
}
