@import url('https://fonts.googleapis.com/css?family=Press+Start+2P');
$font-family-game: 'Press Start 2P', cursive;

// VARS

$screen-width: 1000px;
$screen-height: 750px;
$color-explode-player: #ec1d24;

// GAME

.game-open {
    display: none;
}

.game-menu {
    color: $color-white;
    perspective: $space-and-two-thirds * 3;

    .game-menu__header {
        margin: $space-quadruple 0;
        text-align: center;
        transform: rotateX(-20deg);
    }

    .game-menu__primary-heading {
        font-family: $font-family-game;
        font-size: $alpha;
    }

    .game-menu__secondary-heading {
        font-family: $font-family-game;
        margin-top: 0;
    }

    .game-menu__start {
        margin-top: $space-triple;
        text-align: center;
    }

    .game-menu__group {
        display: block;
        text-align: center;

        &--enemy {
            display: inline-block;
            height: $space-and-two-thirds;
            width: $space-triple + $space-third;
        }

        &--points {
            display: inline-block;
            padding: $space-two-thirds 0;
        }

    }
}

.game-screen {
    font-family: $font-family-game;
    height: $screen-height;
    margin: 0 auto;
    overflow: hidden;
    transform: scale(.9);
    width: $screen-width;

    @include respond-max($x-large-width) {
        display: none;
    }
}

.game-ui {
    margin-top: $space-third;

    .game-ui__menu {
        color: $color-white;
        display: flex;
        justify-content: space-around;
        text-align: center;
        width: $screen-width;
    }

    .game-ui__text {
        display: inline-block;
        margin-bottom: $space-third;
    }
}

.game-over {
    color: $color-white;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    text-align: center;

    .game-over__text-group {
        transform: translateY(-$space-double);
    }

    .game-over__primary-header {
        font-family: $font-family-game;
        font-size: $alpha;
        margin-top: $space-quadruple;
    }

    .game-over__secondary-header {
        font-family: $font-family-game;
        font-size: $normalis;
    }
}

.load-level {
    color: $color-white;
    text-align: center;

    .load-level__heading-primary {
        font-family: $font-family-game;
        font-size: $alpha;
        margin-top: $space-quadruple;
    }

    .load-level__heading-secondary {
        font-family: $font-family-game;
        font-size: $normalis;
    }
}

.position-component {
    position: absolute;
}

.bullet-asset {
    background-color: $color-white;
}

.enemy-bullet-asset {
    background-color: $color-explode-player;
}

.player-asset {
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}

.player-asset.explode,
.enemy-asset.explode,
.boss-asset.explode {
    background-position: center;
    background-repeat: no-repeat;
}

.enemy-asset {
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}

.obstacle-asset-full-0 {
    background-color: $color-white;
}

.obstacle-asset-destroy {
    visibility: hidden;
}

.boss-asset {
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}
