.widget--case-study {
    position: relative;
}

.case-study {
    .container {
        position: relative;
    }

    .case-study__container {
        > .case-study__section {
            &:first-child {
                margin-top: $space-quadruple;
            }
        }
    }

    .scroll-nav__heading {
        @extend %visually-hidden;
    }

    .scroll-nav {
        @extend %container;
        @extend %container--flush;

        margin-left: -$space-and-third;
        pointer-events: none;
        position: absolute;
        top: 0;
        width: 100%;
        z-index: 1;

        @include respond-max($xx-large-width) {
            display: none;
        }

        @include respond-min($xxx-large-width) {
            margin-left: 0;
        }

        &.fixed {
            position: fixed;
            top: $space-and-third;
        }

        &.remove-fixed {
            position: absolute;
        }
    }

    .scroll-nav__list {
        float: right;
        list-style: none;
        margin: 0 ($space-and-third * 2);
        pointer-events: auto;

        @include respond-min($xxx-large-width) {
            margin: 0;
        }

        &::before {
            background-color: $color-white;
            box-shadow: $border-width-small 0 0 0 rgba($color-primary, .5);
            content: '';
            display: block;
            height: $space-quadruple + $space;
            margin: 0 auto $space;
            width: $border-width-mid;
        }
    }

    .scroll-nav__item {
        margin: 0;
        width: $space-double;

        &.active {
            .scroll-nav__link {
                width: $space-double;
            }
        }
    }

    .scroll-nav__link {
        display: block;
        height: $space;
        margin: 0 auto;
        overflow: hidden;
        padding: 0;
        text-indent: -9999px;
        transition: width $transition-speed-fast $transition-cubic-bezier;
        width: $space;

        &:hover,
        &:focus {
            &::before {
                background-color: $color-secondary;
            }
        }

        &::before {
            background-color: $color-white;
            box-shadow: 0 $border-width-small 0 0 rgba($color-primary, .5);
            content: '';
            display: block;
            height: $border-width-mid;
            left: 0;
            position: absolute;
            top: 50%;
            transition: background-color $transition-speed-fast $transition-cubic-bezier;
            width: 100%;
        }

        &::after {
            content: none;
        }
    }

    .scroll-nav__section {
        &:first-child {
            .case-study__section {
                margin-top: $space-quadruple;
            }
        }
    }

    .case-study__section {
        margin-top: $space-quadruple;
        position: relative;

        @include respond-min($large-width) {
            align-items: flex-end;
            display: flex;
            margin-top: $space * 9;
            padding-bottom: $space * 6;
        }
    }

    .case-study__section--alternate {
        justify-content: flex-end;

        .case-study__content {
            @include respond-min($large-width) {
                margin: 0 (-$space * 6) (-$space * 6) 0;
                order: -1;
            }
        }
    }

    .case-study__image {
        transition: transform $transition-speed-slow $transition-cubic-bezier;
        width: 100%;
    }

    .case-study__image-link {
        display: block;
        margin-bottom: $space-double;
        overflow: hidden;
        padding: 0;

        @include respond-min($large-width) {
            float: left;
            margin: 0;
            width: percentage(4 / 7);
        }

        &:hover {
            .case-study__image {
                transform: scale(1.05);
            }
        }

        &::after {
            content: none;
        }
    }

    .case-study__image-link-2 {
        bottom: -$space-double;
        flex: none;
        overflow: visible;
        pointer-events: none;
        position: absolute;
        right: 0;
        transform: translateY(var(--translateY));
        transition: transform $transition-speed-fast $transition-cubic-bezier;
        width: 50%;
        will-change: transform;
        z-index: 2;

        @include respond-max($large-width) {
            display: none;
        }

        &:hover {
            .case-study__image {
                transform: none;
            }
        }

        .no-js & {
            transform: translateY($space-double);

        }

        .case-study__image {
            width: auto;
        }

        .link {
            pointer-events: auto;

            &::after {
                content: none;
            }
        }
    }

    .case-study__content {
        background-color: $color-grey-dark;
        margin: (-$space-quadruple) 0 0;
        padding: $space-double;
        transition: background-color $transition-speed-slow $transition-cubic-bezier;
        z-index: 1;

        @include respond-min($small-width) {
            margin: (-$space-quadruple) $space 0;
        }

        @include respond-min($large-width) {
            float: left;
            margin: 0 0 (-$space * 6) (-$space * 6);
            padding: $space-triple + $space-two-thirds;
            width: percentage(4 / 7);
        }

        @include respond-min($xx-large-width) {
            width: percentage(3 / 7);
        }

        &:hover {
            background-color: darken($color-grey-dark, 8%);
        }

        &::after {
            content: none;
        }

        &.fade-in {
            .js & {
                transition: transform $transition-speed-fast $transition-cubic-bezier,
                background-color $transition-speed-slow $transition-cubic-bezier;
            }
        }
    }

    .case-study__heading {
        @extend %normalis;

        font-weight: $font-weight-semi-bold;
        letter-spacing: normal;
        margin-top: 0;
    }

    .case-study__subheading {
        @extend %beta;

        margin: $space-half 0 $space-double;
    }

    .case-study__subheading-link {
        display: inline;
        letter-spacing: $letter-spacing-small;
        padding: 0;
        text-transform: none;
        white-space: normal;
    }

    .case-study__link {
        font-weight: $font-weight-semi-bold;
    }
}
