.button--warning,
%button--warning {
    &,
    &:visited {
        background-color: $color-warning;
        border-color: darken($color-warning, 10%);
        color: $color-white;
    }

    &:hover,
    &:active,
    &:focus {
        background-color: lighten($color-warning, 5%);
    }

    &::after {
        content: none;
    }
}
