// ========================================================
//  Type
// ========================================================
// Apply various font sizes by class. Pragmatic & practical font sizing
// as per: http://csswizardry.com/2012/02/pragmatic-practical-font-sizing-in-css/

// ========================================================
//  Type sizes
// ============
//  Oversized Type Range
// ======================
%giga,
.giga {
    font-size: $giga;
    font-weight: $font-weight-light;
    letter-spacing: $letter-spacing-small;
    line-height: $line-height-giga;
    margin: $space-half 0;

    @include respond-max($small-width) {
        font-size: $giga-small;
    }
}

%mega,
.mega {
    font-size: $mega;
    font-weight: $font-weight-bold;
    letter-spacing: $letter-spacing-small;
    line-height: $line-height-mega;
    margin: $space-half 0;
    text-transform: uppercase;
}

%kilo,
.kilo {
    font-size: $kilo;
    letter-spacing: $letter-spacing-small;
    line-height: $line-height-kilo;
    margin: $space 0 0;

    @include respond-max($mid-width) {
        font-size: $beta;
    }
}

//  Standard Type Range
// =====================
%alpha,
.alpha {
    font-size: $alpha;
    letter-spacing: $letter-spacing-small;
    line-height: $line-height-alpha;
    margin: $space 0 0;

    @include respond-max($mid-width) {
        font-size: $beta;
    }
}

%beta,
.beta {
    font-size: $beta;
    font-weight: $font-weight-bold;
    letter-spacing: $letter-spacing-small;
    line-height: $line-height-beta;
    margin: $space 0 0;
}

%gamma,
.gamma {
    font-size: $gamma;
    line-height: $line-height-gamma;
    margin: $space 0 0;
}

%delta,
.delta {
    font-size: $delta;
    letter-spacing: $letter-spacing-mid;
    line-height: $line-height-delta;
    margin: $space 0 0;
}

%epsilon,
.epsilon {
    font-size: $epsilon;
    line-height: $line-height-epsilon;
    margin: $space 0 0;
}

%zeta,
.zeta {
    font-size: $zeta;
    line-height: $line-height-zeta;
    margin: $space 0 0;
}

//  Base Type Size
// ================
%normalis,
.normalis {
    font-size: $normalis;
    font-weight: $font-weight-light;
    line-height: $line-height-normalis;

    @include respond-max($large-width) {
        font-size: $zeta;
    }
}

//  Base Type Reset
// =================
%type-reset {
    font-family: $font-family;
    font-size: $normalis;
    line-height: $line-height-ratio;
}

//  Small Type Range
// ==================
%milli,
.milli {
    font-size: $milli;
    font-weight: $font-weight-light;
}

%micro,
.micro {
    font-size: $micro;
}

// ========================================================
//  Headings
// ==========
//  Heading styling. All headings are also available as
//  classes. Eg: .h1, .h2 etc
//  Ref: http://csswizardry.com/2012/02/pragmatic-practical-font-sizing-in-css/
h1,
h2,
h3,
h4,
h5,
h6 {
    color: $color-text-default;
    font-family: $font-family-heading;
    font-weight: $font-weight-light;
}

h1,
%h1 {
    @extend %kilo;

    font-weight: $font-weight-bold;

    @include respond-max($mid-width) {
        font-size: $gamma;
    }
}

h2 {
    @extend %delta;

    font-weight: $font-weight-semi-bold;

    h1 + & {
        margin-top: $space-third;
    }
}

h3 {
    @extend %delta;

    h2 + & {
        margin-top: $space-third;
    }
}

h4 {
    @extend %zeta;

    font-weight: $font-weight-semi-bold;

    h3 + & {
        margin-top: $space-third;
    }
}

h5 {
    @extend %normalis;

    font-weight: $font-weight-semi-bold;
    letter-spacing: $letter-spacing-x-large;
    text-transform: uppercase;

    h4 + & {
        margin-top: $space-third;
    }
}

h6 {
    @extend %normalis;

    letter-spacing: $letter-spacing-x-large;
    text-transform: uppercase;

    h5 + & {
        margin-top: $space-third;
    }
}

@for $i from 1 through 6 {
    %h#{$i},
    .h#{$i} {
        @extend h#{$i};

        display: block;
    }
}

// ========================================================
//  Paragraphs
// ============
p {
    @extend %zeta;

    margin: $space-two-thirds 0 0;

    a {
        letter-spacing: normal;
        padding-bottom: 0;
        text-transform: none;

        &::after {
            bottom: $border-width-large;
            height: $border-width-small;
        }

        &:hover,
        &:active {
            &::after {
                height: percentage(1 / 2.8); // visually looks central
            }
        }
    }

    h2 + & {
        margin-top: $space;
    }

    &:first-child {
        &:not(.summary) {
            margin-top: 0;
        }
    }
}

.summary {
    @extend %epsilon;

    font-weight: $font-weight-semi-bold;
    max-width: $content-max-width;
    padding: 0;
    position: relative;
}

.small-text,
%small-text {
    &,
    p,
    li {
        @extend %normalis;
    }
}

// ========================================================
//  Lists
// =======
//  Nested - Lists
// ================
li {
    @extend %zeta;

    margin-top: $space-third;

    ul,
    ol {
        margin-bottom: 0;
        margin-top: 0;
    }

    a {
        text-transform: none;
    }
}

// ========================================================
//  Address
// =======

.address {
    font-style: normal;

    .address__title {
        @extend %zeta;

        font-weight: $font-weight-semi-bold;
        letter-spacing: $letter-spacing-large;
        margin-top: 0;
        text-transform: uppercase;
    }

    .address__text {
        font-weight: $font-weight-light;
        margin-bottom: $space-two-thirds;
    }

    .address__link {
        @extend %zeta;

        font-weight: $font-weight-semi-bold;
        text-transform: none;
    }
}
