// Styleguide 18.4

.site-footer {
    @extend %module;

    text-align: center;

    .container {
        padding: $space-double $space-and-third;

        @include respond-min($mid-width) {
            margin: $space-quadruple auto 0;
        }
    }

    .site-footer__links {
        margin-bottom: $space-and-third;

        .list__item {
            display: inline-block;
            margin: 0 $space-half $space;
            width: auto;
        }

        .list__link {
            @include resetLinks;
        }
    }

    .site-footer__sign-up {
        .link {
            font-weight: $font-weight-semi-bold;

            &::after {
                height: 0;
            }

            &:hover {
                &::after {
                    height: $border-width-small;
                }
            }
        }
    }

    .site-footer__further-info {
        margin-top: $space-double;

        @include respond-min($mid-width) {
            align-items: flex-end;
            display: flex;
            justify-content: space-between;
            margin-top: $space-triple + $space-and-half;
        }
    }

    .site-footer__rights {
        @extend %milli;

        margin: 0;

        @include respond-min($mid-width) {
            text-align: left;
            width: percentage(1 / 3.3); // want both the left and right columns to be the same size so the central item sits in the centre, this size works for the size of the copyright
        }
    }

    .site-footer__social {
        @include respond-max($mid-width) {
            margin: $space 0;
        }
    }

    .site-footer__legal {
        @include respond-min($mid-width) {
            text-align: right;
            width: percentage(1 / 3.3); // want both the left and right columns to be the same size so the central item sits in the centre, this size works for the size of the copyright
        }

        .link {
            @extend %milli;

            text-transform: none;

            &::after {
                height: 0;
            }

            &:hover {
                &::after {
                    height: $border-width-small;
                }
            }

            &:first-child {
                margin-right: $space;
            }
        }
    }
}
