// Team widget
.team {
    .team__container {
        @extend %cf;

        background-color: $color-white;
        margin-top: $space-quadruple;
        position: relative;
    }

    .team__content {
        height: 100%;
        padding: $space-quadruple $space-double;

        @include respond-min($xx-large-width) {
            float: left;
            left: 0;
            position: absolute;
            top: 0;
            width: percentage(2 / 8);
        }
    }

    .team__subheading {
        color: $color-primary;
        font-family: $font-family-condensed;
        font-weight: $font-weight-bold;
        letter-spacing: $letter-spacing-large;
        text-transform: uppercase;
    }

    .team__text {
        @extend %epsilon;

        color: $color-primary;
        font-weight: $font-weight-light;
        letter-spacing: $letter-spacing-mid;
    }

    .team__grid {
        float: left;
        width: 100%;

        &::after {
            content: 'flickity';
            display: none;
        }

        .flickity-viewport {
            cursor: url('./../images/cursor.png'), grab;

            &.is-pointer-down {
                cursor: url('./../images/cursor-clicked.png'), grab;
            }
        }
    }

    .team__grid-slide {
        @extend %cf;

        display: flex;
        flex-wrap: wrap;
        float: left;
        min-height: 100%;
        width: 90%;

        @include respond-min($mid-width) {
            width: 45%;
        }

        @include respond-min($xx-large-width) {
            width: percentage(3 / 8);
        }
    }

    .team__grid-slide--full {
        height: 100%;

        .team__grid-item {
            background-position: center;
            background-size: cover;
            height: 100%;
            width: 100%;
        }

        .team__grid-content {
            background-color: $color-grey-light;
            padding: $space-and-half;
            transform: translate3d(0, 0, 0);
        }
    }

    .team__grid-item {
        float: left;
        height: percentage(1 / 2);
        position: relative;
        width: percentage(1 / 2);

        &:nth-child(3) {
            clear: both;
        }
    }

    .team__grid-content {
        background-color: $color-off-white;
        bottom: 0;
        left: 0;
        min-height: $space-quadruple;
        padding: $space-third $space;
        position: absolute;
        width: 100%;
        z-index: 1;
    }

    .team__grid-image {
        height: 100%;
        margin-bottom: $space-double;
        object-fit: cover;
        width: 100%;
    }

    .team__grid-video-container {
        height: 100%;
        left: 0;
        overflow: hidden;
        position: absolute;
        top: 0;
        width: 100%;

        &.play-video {
            .team__grid-video {
                display: block;
            }
        }
    }

    .team__grid-video-control {
        left: $space;
        position: absolute;
        top: $space;
        transform: translate3d(0, 0, 0);
        z-index: 1;

        &:hover {
            &::before {
                border-color: $color-white;
            }
        }

        &:focus,
        &:active {
            outline: none;

            &::before {
                border-color: $color-white;
                box-shadow: 0 0 0 $border-width-large $color-secondary;
            }
        }

        &::before {
            border: $border-width-large solid $color-standout;
            border-radius: 100%;
            content: '';
            height: 150%;
            left: 50%;
            position: absolute;
            top: 50%;
            transform: translate(-50%, -50%);
            transition: transform $transition-speed-fast $transition-cubic-bezier,
                        border $transition-speed-fast $transition-cubic-bezier;
            width: 150%;
        }
    }

    .team__grid-video {
        display: none;
        height: 100%;
        left: 50%;
        min-width: 100%;
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
        width: auto;
    }

    .team__grid-heading {
        color: $color-primary;
        letter-spacing: $letter-spacing-mid;
        line-height: 1.3;
        margin: 0;
    }

    .team__grid-text {
        color: $color-primary;
        font-weight: $font-weight-light;
        letter-spacing: $letter-spacing-mid;
        line-height: 1;
        margin: 0;
    }

    .team__grid-linkedin {
        cursor: pointer;
        display: inline-block;
        margin-top: $space;
        padding: 0;

        &:hover {
            &::before {
                height: 150%;
                opacity: 1;
                width: 150%; // hover transition ending size, needs to be larger than icon - this spacing looks good
            }
        }

        &::before {
            border: $color-primary solid $border-width-large;
            border-radius: 100%;
            content: '';
            height: 10%;
            left: 50%;
            opacity: 0;
            position: absolute;
            top: 50%;
            transform: translate(-50%, -50%);
            transition: $transition-speed-fast $transition-cubic-bezier;
            width: 10%; // hover transition starting size
        }

        &::after {
            content: none;
        }
    }
}
