$video-minus-margin: -250px;
$mask-width: 918px;
$hero-max-size: 320px;

.home-hero {
    .container {
        position: relative;
    }

    .home-hero__content {
        position: relative;
        z-index: 1;

        @include respond-min($large-width) {
            padding-left: $space-and-three-quarters + $space-eighth;
            position: absolute;
            width: percentage(1 / 2.5);
            z-index: 1;
        }
    }

    // scss-lint:disable ImportantRule

    .home-hero__video-container {
        background: url('../images/loader.gif') no-repeat 53% 54%;
        margin: 0 auto;
        max-width: $hero-max-size;
        position: relative;
        z-index: 1;

        @include respond-min($mid-width) {
            float: right;
            left: 0;
            max-width: $mask-width;
            top: -$space-quadruple;
            width: 55%; // visually this size works best on resize
        }

        @include respond-min($xx-large-width) {
            margin-bottom: $video-minus-margin !important;
            top: $video-minus-margin;
            width: 100%;
        }

        &::before {
            border: $border-width-large solid $color-primary;
            bottom: -$border-width-mid;
            content: '';
            left: -$border-width-mid;
            position: absolute;
            right: -$border-width-mid;
            top: -$border-width-mid;
            z-index: 1;
        }
    }

    .home-hero__overflow {
        overflow: hidden;
        position: relative;
    }

    // scss-lint:enable ImportantRule

    .home-hero__video-mask {
        @include respond-min($mid-width) {
            background-image: url('../images/mask.png');
            background-position: right;
            background-repeat: no-repeat;
            background-size: cover;
            height: 100%;
            position: absolute;
            right: 0;
            top: 0;
            width: 100%;
        }
    }

    .home-hero__video-img {
        margin: 0 auto;
        max-height: 100%;
        width: 100%;
    }

    .home-hero__video {
        height: auto;
        left: 50%;
        height: 100%;
        min-width: 100%;
        overflow: hidden;
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
        width: auto;
        z-index: -1;

        .js & {
            opacity: 0;
            transition: opacity $transition-speed-slow $transition-cubic-bezier;

            &.is-visible {
                opacity: 1;
            }
        }
    }

    .home-hero__heading {
        @extend %alpha;

        color: $color-grey;
        margin-bottom: $space-and-half;

        @include respond-max($mid-width) {
            margin-top: $space-triple;
        }
    }

    .home-hero__list {
        .list__link {
            @include resetLinks;
        }
    }

    .pause-video {
        bottom: $space-quadruple + $space-third;
        color: $color-white;
        left: 0;
        margin: 0 auto;
        pointer-events: none;
        position: absolute;
        text-align: center;
        width: 100%;
        z-index: 1;

        @include respond-max($mid-width) {
            bottom: auto;
        }
    }

    .pause-video__button {
        padding: $space-twelfth;
        pointer-events: auto;
        position: relative;

        &:hover,
        &:focus,
        &:active {
            &::before {
                border-color: $color-white;
            }
        }

        &:focus,
        &:active {
            outline: none;

            &::before {
                box-shadow: 0 0 0 $border-width-large $color-secondary;
            }
        }

        &::before {
            border: $border-width-mid solid $color-grey-light;
            border-radius: 100%;
            content: '';
            height: 150%;
            left: 50%;
            position: absolute;
            top: 50%;
            transform: translate(-50%, -50%);
            transition: border-color $transition-speed $transition-cubic-bezier;
            width: 150%;
        }
    }
}
