$logo-height:              30px;
$logo-width:               194px;

.popup {
    @extend %cf;

    background-color: $color-primary;
    display: none;
    height: 100vh;
    left: 0;
    padding-bottom: $space-double;
    position: fixed;
    top: 0;
    transition: opacity $transition-speed-slow $transition-cubic-bezier;
    width: 100%;
    z-index: 3;

    &.is-open {
        display: block;

        .popup__container {
            opacity: 0;
        }
    }

    &.is-visible {
        .popup__container {
            opacity: 1;
        }
    }

    .no-js & {
        background-color: transparent;
        display: block;
        height: auto;
        position: relative;
    }

    .popup__container {
        @extend %container;

        overflow-y: auto;
        width: 100%;
    }

    .popup__close-container {
        max-width: $full-width;
        padding: $space-and-third;

        @include respond-min($large-width) {
            padding: ($space-double + $space-quarter) $space-and-third;
        }
    }

    .popup__content {
        @include respond-max($xxx-large-width) {
            padding: $space;
        }
    }

    .popup__form {
        @extend %cf;

        @include respond-min($xxx-large-width) {
            margin-top: $space-quadruple;
        }

        .form__control {
            @include respond-min($large-width) {
                margin: $space-third 0;
            }

            &:first-child {
                margin-top: 0;
            }
        }

        .form__control--space-top {
            margin-top: $space;
        }

        .form__label {
            @include respond-min($large-width) {
                display: inline;
                margin-right: $space-half;
            }
        }

        .form__field,
        .form__select,
        .custom-select {
            @include respond-min($large-width) {
                display: inline-block;
                margin-top: 0;
                width: $space-quadruple * 4;
            }
        }

        .form__button {
            margin-top: $space-and-half;
        }

        .form__note {
            margin: $space-double 0 $space;
        }
    }

    .popup__form-col {
        @include respond-min($xxx-large-width) {
            float: left;

            &:first-child {
                padding-right: $space;
                width: percentage(1 / 4);
            }

            &:last-child {
                width: percentage(3 / 4);
            }
        }
    }

    .js & {
        .popup__container {
            transform: translateY($space-two-thirds);
            transition: transform $transition-speed-slow $transition-cubic-bezier,
                        opacity $transition-speed-slow $transition-cubic-bezier;
        }

        &.is-visible {
            .popup__container {
                transform: translateY(0);
                transition-delay: $transition-speed-faster;
            }
        }
    }

    .identity__link {
        float: left;
        margin: $border-width-large 0;
        padding: 0;
        z-index: 3;

        &::after {
            content: none;
        }
    }

    .identity__logo {
        height: $logo-height;
        width: $logo-width;
    }
}

.popup-callout {
    background-color: $color-standout;
    border: 0;
    padding: $space-triple $space;
    text-align: center;
    transition: background-color $transition-speed-fast $transition-cubic-bezier;
    width: 100%;

    &:hover,
    &:active {
        background-color: $color-link-border-active;
    }

    &:focus {
        outline: $border-width-large solid $color-link-border-active;
    }

    &[data-a11y-toggle]:not([aria-controls]) {
        display: block;

        .no-js & {
            display: none;
        }
    }

    .popup-callout__heading {
        @extend %mega;

        letter-spacing: $letter-spacing-small;
        margin: 0;

        @include respond-max($mid-width) {
            font-size: $kilo;
            line-height: $line-height-kilo;
        }
    }

    .popup-callout__text {
        @extend %delta;

        font-weight: $font-weight-bold;
        letter-spacing: normal;
        margin: 0;
        text-transform: uppercase;

        @include respond-max($mid-width) {
            font-size: $zeta;
            line-height: $line-height-zeta;
        }
    }
}
