.hero {
    position: relative;

    .hero__image {
        background-position: center;
        background-size: cover;
        min-height: 40em;
        position: relative;

        &::before {
            background-color: $color-primary;
            content: '';
            height: 100%;
            left: 0;
            opacity: .3;
            position: absolute;
            top: 0;
            width: 100%;
        }
    }

    .hero__content {
        left: 50%;
        position: absolute;
        text-align: center;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 80%;

        .overlay-header & {
            margin-top: -$space-two-thirds; // make up for space lost to overlay
        }
    }

    .hero__content--right {
        @include respond-min($large-width) {
            left: auto;
            right: 0;
            text-align: left;
            width: percentage(2 / 7);

            .hero__divider {
                &::before {
                    left: 0;
                    transform: none;
                }
            }
        }
    }

    .hero__heading {
        @extend %kilo;

        font-weight: $font-weight-bold;
        margin-top: 0;
    }

    .hero__subheading,
    .hero__date {
        @extend %normalis;

        display: inline-block;
        font-weight: $font-weight-semi-bold;
        letter-spacing: normal;
        margin: 0;
        position: relative;
        text-transform: uppercase;
    }

    .hero__date {
        margin-left: $space;
    }

    .hero__divider {
        margin-top: $space-and-two-thirds;
        padding-top: $space-two-thirds;
        position: relative;

        &::before {
            background-color: $color-white;
            content: '';
            height: $border-width-small;
            left: 50%;
            position: absolute;
            top: 0;
            transform: translateX(-50%);
            width: $space-double;
        }
    }

    .hero__supporting-image {
        margin-bottom: 0;
        position: relative;
        transform: translateY(-50%);
        transition: margin-bottom $transition-speed-slow $transition-cubic-bezier; // stop jumping after js load
        width: 100%;
    }

    .hero__supporting-img {
        margin: 0 auto;
        max-width: 90%;
    }
}
