// journal widget
.journal {
    .journal__heading {
        @extend %delta;

        color: $color-primary;
        letter-spacing: $letter-spacing-large;
    }

    .journal__grid {
        margin-top: $space-and-sixth;
        text-align: center;
    }

    .journal__grid-item {
        display: inline-block;
        margin: $space-half;
        width: calc(#{percentage(1 / 3)} - #{$space});

        @include respond-max($mid-width) {
            width: calc(50% - #{$space});
        }

        @include respond-max($small-width) {
            margin: $space-half 0;
            width: 100%;
        }
    }

    .journal__grid-img {
        width: 100%;
    }

    .journal__grid-link {
        @include linkUnderline('.journal__grid-heading', $color-white, transparent);

        display: block;
        padding: 0;
        position: relative;

        &:hover {
            &::before {
                background-color: $color-secondary;
                height: 104%; // before element grows bigger than container on hover
                opacity: 1;
                width: 104%;
            }
        }

        &:active {
            &::before {
                background-color: $color-tertiary;
                height: 104%;
                opacity: 1;
                width: 104%;
            }
        }

        &::before {
            background-color: $color-primary;
            bottom: 0;
            content: '';
            display: block;
            height: 100%;
            left: 50%;
            opacity: .3;
            position: absolute;
            right: 0;
            top: 50%;
            transform: translate(-50%, -50%);
            transition: all $transition-speed $transition-cubic-bezier;
            width: 100%;
        }

        &::after {
            content: none;
        }
    }

    .journal__grid-content {
        bottom: 0;
        left: 0;
        padding: $space;
        position: absolute;
        text-align: left;
        white-space: normal;
        width: 100%;
    }

    .journal__grid-heading {
        @extend %epsilon;

        display: inline;
        font-weight: $font-weight-bold;
        line-height: 1.3;
        text-transform: uppercase;
    }

    .journal__grid-text {
        @extend %normalis;

        color: $color-white;
        font-weight: $font-weight-normal;
        letter-spacing: $letter-spacing-mid;
        line-height: 1.65;
        margin-top: $space-quarter;
        text-transform: uppercase;
    }

    .journal__load-more {
        display: block;
        font-weight: $font-weight-semi-bold;
        letter-spacing: $letter-spacing-large;
        margin: ($space-triple + $space-half) auto 0;
        text-transform: uppercase;
        transition: color $transition-speed-fast $transition-cubic-bezier;

        &:hover,
        &:focus,
        &:active {
            color: $color-tertiary;

            .icon {
                transform: translateY($space-twelfth);
            }
        }

        &:focus,
        &:active {
            outline: $focus-outline;
        }

        .icon {
            margin: $space-half auto 0;
            transition: transform $transition-speed-fast $transition-cubic-bezier;
        }
    }
}

.js .overlay-header {
    .page-article {
        .widget-row {
            &:first-child {
                position: fixed;
                top: 0;
                width: 100%;
                z-index: -2;
            }

            &:nth-child(2) {
                margin-top: 38em; // matches hero height
            }
        }
    }
}

.journal--carousel {
    .container {
        @include respond-max($large-width) {
            padding: 0;
        }
    }

    .journal__grid {
        @include respond-max($large-width) {
            &::after {
                content: 'flickity';
                display: none;
            }
        }
    }

    .journal__grid-item {
        display: inline-block;
        margin: $space-half;

        @include respond-max($large-width) {
            width: percentage(1 / 2);
        }

        @include respond-max($mid-width) {
            width: percentage(4 / 5);
        }
    }
}
