// Content widget
.content {
    &.fade-in-delay {
        transform: translateY(var(--translateY));
        transition: transform $transition-speed-fast $transition-cubic-bezier;
        will-change: transform;
    }

    .content__heading {
        @extend %h1;

        font-family: $font-family-condensed;
        letter-spacing: $letter-spacing-large;
        margin-bottom: $space-triple;
        text-align: left;

        .widget-width--100 & {
            text-align: center;
        }

        + .editor > p:first-child {
            @extend %epsilon;

            font-weight: $font-weight-semi-bold;

            .widget-width--33 & {
                @extend %zeta;

                font-weight: $font-weight-normal;
            }
        }
    }

    .content__heading--page-heading {
        font-family: $font-family-heading;
        text-transform: none;

        &,
        &:first-child {
            margin-top: $space-double;

            @include respond-min($large-width) {
                margin-top: $space-third;
            }
        }

        + .editor > p:first-child {
            @extend %normalis;

            font-weight: $font-weight-light;
            margin: 0;
        }
    }

    .content__heading.content__heading--red {
        @extend %beta;

        color: $color-secondary;
        font-family: $font-family-heading;
        font-weight: $font-weight-light;
        line-height: 1.68;
        margin-top: 0;
        text-transform: none;

        @include respond-max($mid-width) {
            font-size: $gamma;
        }

        + .editor > p:first-child {
            @extend %normalis;

            margin: 0;
        }
    }

    // scss-lint:disable ImportantRule
    .content__heading.content__heading--red-with-dash {
        @extend .content__heading--red;

        font-weight: $font-weight-normal;
        margin-bottom: $space-and-half;
        text-align: left !important; // always want a title with a dash to be aligned left

        &::before {
            background-color: $color-white;
            content: '';
            display: inline-block;
            height: $border-width-mid;
            margin-right: $space;
            position: relative;
            top: -$space-hard;
            width: $space-double;
        }
    }
    // scss-lint:enable ImportantRule

    .content__heading--small {
        @extend %delta;

        font-weight: $font-weight-semi-bold;
    }

    .inner-container-mid {
        p {
            @extend %normalis;

            font-weight: $font-weight-light;

            @include respond-min($mid-width) {
                column-gap: $space-double;
            }
        }
    }

    .inner-container-small {
        p {
            @extend %normalis;

            font-weight: $font-weight-light;
        }
    }

    .widget-row--white &,
    .widget-row--grey &,
    .widget-row--padding & {

        .content__heading {
            @extend %delta;

            font-weight: $font-weight-semi-bold;
            letter-spacing: $letter-spacing-large;
            margin-top: 0;

            &.content__heading--red {
                @extend %beta;

                font-weight: $font-weight-light;
                margin-top: 0;
            }
        }

        .content__heading.content__heading--large {
            @extend %h1;

            font-family: $font-family-heading;
            margin: 0;
            text-transform: none;
        }

        .content__heading--large-light {
            font-family: $font-family-heading;
            font-size: $beta;
            font-weight: $font-weight-light;
            letter-spacing: $letter-spacing-mid;
            text-transform: none;

            + .editor > p:first-child {
                @extend %zeta;

                font-weight: $font-weight-normal;
            }
        }

        .widget-row--flush & {
            padding: $space-quadruple 0;
        }
    }

    // replace bullet point
    ul {
        margin-left: 0;

        li {
            list-style: none;
            padding-left: $space;
            position: relative;

            &::before {
                background-color: $color-secondary;
                content: '';
                height: $border-width-large * 2;
                left: $space-eighth;
                position: absolute;
                top: $space-half + $space-twelfth;
                width: $border-width-large * 2;
            }
        }
    }

    .button {
        margin-top: $space-two-thirds;
    }
}

.content--large {
    @include respond-min($large-width) {
        text-align: center;
    }

    .content__heading {
        margin-bottom: $space-triple;

        + .editor > p:first-child {
            font-size: $delta;
            font-weight: $font-weight-light;
            line-height: $line-height-delta;
            margin-top: $space-triple + $space-half;
        }
    }

    .content__heading + p,
    p,
    li {
        font-size: $delta;
        font-weight: $font-weight-light;
        line-height: $line-height-delta;
    }

    p {
        margin-top: $space-and-sixth;
    }
}

.widget-row--white {
    background-color: $color-white;
    padding: ($space-quadruple - $space-third) 0;

    &,
    a,
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        color: $color-primary;
    }

    .widget-width--33 {
        .content__heading {
            color: $color-secondary;
            font-weight: $font-weight-light;

            + p {
                @extend %normalis;
                font-weight: $font-weight-light;
            }
        }

        p,
        li {
            @extend %normalis;
        }

        .widget--image {
            + .widget--content {
                margin-top: $space-three-quarters;
            }
        }
    }

    .widget-width--100 {
        .content__heading {
            text-align: center;
        }
    }

    + .widget-row--white {
        padding-top: 0;
        position: relative;

        &::before {
            background-color: $color-white;
            content: '';
            display: block;
            height: $space;
            position: absolute;
            top: 0;
            transform: translateY(-50%);
            width: 100%;
            z-index: -1;
        }
    }
}

.widget-row--grey {
    background-color: $color-off-white;
    padding: ($space-quadruple - $space-third) 0;

    &,
    *,
    a:visited {
        color: $color-primary;
    }
}

.widget-row--padding {
    padding: ($space-quadruple - $space-third) 0;

    .content__heading {
        @extend %beta;

        font-weight: $font-weight-light;
        line-height: 1.68;
        margin-bottom: $space-double;
    }

    .content__heading--small {
        font-weight: $font-weight-semi-bold;
    }

    + .widget-row--padding {
        padding-top: 0;
    }
}
