// multi images widget
.widget--multi-images {
}

.multi-images {
    align-items: flex-end;
    display: flex;

    .image__img {
        float: left;
        width: 100%;
    }

    .multi-images__video {
        max-width: 100%;
    }

    .multi-images__video-btn {
        &:focus,
        &:active {
            outline: $focus-outline;
        }
    }

    .multi-images__video-btn-text {
        align-items: center;
        display: flex;
        flex-direction: column;
        height: 100%;
        justify-content: center;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;

        &:hover,
        &:focus,
        &:active {
            &::before {
                background-color: rgba($color-primary, .9);
            }
        }

        &::before {
            background-color: rgba($color-primary, .7);
            content: '';
            height: 100%;
            left: 0;
            position: absolute;
            top: 0;
            transition: background-color $transition-speed $transition-cubic-bezier;
            width: 100%;
        }

        .icon {
            position: relative;
            z-index: 1;
        }
    }

    .multi-images__video-label {
        @extend %link;

        font-weight: $font-weight-bold;
        margin-top: $space-half;
        position: relative;
        text-transform: uppercase;
        z-index: 1;
    }
}

.multi-images--overlay-top {
    padding-bottom: $space-double;

    .image__image {
        overflow: hidden;
        transform: translateY($space-double);
        width: calc(50% + #{$space});

        &:nth-child(2) {
            margin-left: -$space-double;
            transform: translateY(var(--translateY));
            transition: transform $transition-speed-fast $transition-cubic-bezier;
            will-change: transform;
        }

        .js & {
            opacity: 0;
            position: relative;
            transition: opacity $transition-speed-fast $transition-cubic-bezier;

            &::after {
                background-color: $color-secondary;
                content: '';
                height: 100%;
                left: 0;
                position: absolute;
                top: 0;
                transition: transform $transition-speed $transition-cubic-bezier;
                transition-delay: $transition-speed;
                width: 100%;
            }

            &:first-child {
                transition-delay: $transition-speed-slow;

                &::after {
                    transition-delay: $transition-speed-slow;
                }
            }
        }
    }

    &.multi-images--third {
        .image__image {
            @include respond-min($mid-width) {
                width: 40%;
            }
        }
    }

    &.show-images {
        .image__image {
            opacity: 1;

            &::after {
                transform: translateX(-100%);
            }
        }
    }
}

.multi-images--overlay-bottom {
    padding-bottom: $space-double;

    .image__image {
        width: calc(50% + #{$space});

        &:last-child {
            transform: translate(-$space-double, $space-double);
        }
    }

    &.multi-images--third {
        .image__image {
            @include respond-min($mid-width) {
                width: 40%;
            }
        }
    }
}

.multi-images--overlay-centre {
    align-items: center;
    justify-content: center;

    .image__image {
        max-width: calc(50% + #{$space});

        &:first-child {
            transform: translate($space, var(--translateY));
            transition: transform $transition-speed-fast $transition-cubic-bezier;
            will-change: transform;
            z-index: 1;

            .no-js & {
                transform: translateX($space);
            }
        }

        &:last-child {
            transform: translateX(-$space);
        }
    }

    &.multi-images--third {
        .image__image {
            @include respond-min($mid-width) {
                max-width: 40%;
            }
        }
    }
}

.multi-images--grid {
    margin-top: -$space;
    overflow: hidden;

    .image__image {
        width: percentage(1 / 2);

        @include respond-min($mid-width) {
            width: percentage(1 / 3);
        }

        @include respond-min($x-large-width) {
            width: percentage(1 / 4);
        }

        @include respond-min($xxx-large-width) {
            width: percentage(1 / 5);
        }

        &:nth-child(3) {
            display: none;

            @include respond-min($mid-width) {
                display: block;
            }
        }

        &:nth-child(4) {
            display: none;

            @include respond-min($x-large-width) {
                display: block;
            }
        }

        &:nth-child(5) {
            display: none;

            @include respond-min($xxx-large-width) {
                display: block;
            }
        }
    }
}
