// ========================================================
//  Item Lists
// ========================================================

//  Base style
// ============
.list,
%list {
    line-height: 1.3;
    list-style: none;
    margin-left: 0;
}

.list__item,
%list__item {
    @extend %normalis;

    margin-top: remToEm($space-half);
}

.list__item--active-child {
    font-weight: $font-weight-bold;
}

.list__link,
%list__link {
    @extend %normalis;
    @include linkUnderline(null, $color-secondary, $color-primary);

    background-position: 0 -#{$border-width-small};
    color: $color-link;
    font-weight: $font-weight-semi-bold;
    margin-top: 0;
    padding-bottom: 0;
    text-transform: none;
    white-space: normal;

    &.is-active {
        font-weight: $font-weight-bold;
    }

    &::after {
        content: none;
    }
}
