// Image widget
.widget--image {
    .widget-width--50 > & {
        @include respond-min($mid-width) {
            margin-right: -$space;
            padding-left: $space;
        }
    }

    .widget-width--50:first-of-type > & {
        @include respond-min($mid-width) {
            margin-left: -$space;
            margin-right: 0;
            padding-left: 0;
            padding-right: $space;
        }
    }
}

.image {
    .image__img {
        margin: 0 auto;
        width: 100%;

        .living-wage & {
            width: auto;
        }
    }
}
