// Links
//
// Base link styling
// See sass/objects/_link.scss.
//
// :hover       - Hover state.
// :active      - Active state.
// :focus       - Focus state.
//
// Markup:
// <a class="{$modifiers}">Link</a>
//
// Styleguide 11

a,
.link,
%link {
    color: $color-link;
    display: inline-block;
    letter-spacing: $letter-spacing-large;
    padding-bottom: $space-twelfth;
    position: relative;
    text-transform: uppercase;
    white-space: nowrap;
    z-index: 0;

    &::after {
        background-color: $color-secondary;
        bottom: 0;
        content: '';
        display: block;
        height: $border-width-large;
        left: 0;
        position: absolute;
        transition: height $transition-speed-fast $transition-cubic-bezier;
        width: 100%;
        z-index: -1;
    }

    &,
    &:visited {
        text-decoration: none;
    }

    &:visited {
        color: $color-link-visited;
    }

    &:hover,
    &:active {
        &::after {
            height: calc(50% + (#{$space-twelfth} / 2));
        }
    }

    &:active {
        &::after {
            background-color: $color-link-border-active;
        }
    }

    &:focus {
        outline: $focus-outline;
    }
}

button {
    background: transparent;
    border: 0;
    padding: 0;
}
