.link-list {
    .link-list__list {
        @extend %list--rich;

        margin-top: $space-quadruple;
        padding-bottom: $space-double;

        .list__item {
            &,
            &:first-child {
                border-color: $color-primary;
                padding: 0;
            }
        }

        .list__link {
            @extend %delta;
            @include linkUnderlineReset();

            display: block;
            font-weight: $font-weight-light;
            letter-spacing: normal;
            margin: 0;
            padding: ($space - $space-sixth) $space-and-half;
            transition: all $transition-speed $transition-cubic-bezier;

            &:hover,
            &:focus,
            &:active {
                background-color: $color-secondary;
                color: $color-white;
            }

            &:active {
                background-color: $color-tertiary;
            }
        }

        .list__date {
            @extend %normalis;

            float: right;
            line-height: 2.5;

            @include respond-max($small-width) {
                display: block;
                float: none;
            }
        }
    }

    &.fade-in-delay {
        transform: translateY(var(--translateY));
        transition: transform $transition-speed-fast $transition-cubic-bezier;
        will-change: transform;
    }
}
