// ========================================================
//  Brand palette
// ===============

//  Primary palette
// =================
$primary-palette: (
    'red':                      #f33a4f,
    'dark-red':                 #d62246,
    'charcoal':                 #1e252a
);

//  Neutral palette
// =================
$color-black:                   #000;
$color-grey-dark:               #54585b;
$color-grey:                    #9b9b9b;
$color-grey-light:              #c2c0bf;
$color-off-white:               #ededed;
$color-white:                   #fff;

// ========================================================


// ========================================================
//  JDU brand palette
// ====================
$color-jadu-blue:               #95bed9;
$color-jadu-blue-dark:          #2080c0;
$color-spacecraft-yellow:       #ffe807;

// ========================================================


// ========================================================
//  Non-brand palette
// ===================

//  UI palette
// ============
//  These can updated to use colours from the `Primary palette`
$ui-palette: (
    'blue':                    #226fb4,
    'blue-dark':               #095294,
    'green':                   #2eb156,
    'orange':                  #e98300,
    'purple':                  #6d2077,
    'red':                     #c0232e,
    'yellow':                  #ffe13b
);

// ========================================================




// ========================================================
//  Site Colours
// ==============

//  Primary
// =========
$color-primary:                 map-get($primary-palette, 'charcoal');
$color-primary-offset:          $color-white;

//  Text
// ======
$color-text-default:            $color-white;
$color-text-heading:            $color-white;
$color-text-muted:              $color-grey;
$color-text-disabled:           $color-grey-light;

//  Secondary
// ===========
$color-secondary:               map-get($primary-palette, 'red');
$color-secondary-offset:        $color-white;

//  Tertiary
// ===========
$color-tertiary:               map-get($primary-palette, 'dark-red');
$color-tertiary-offset:        $color-white;

//  Response
// ==========
$color-success:                 map-get($ui-palette, 'green');
$color-warning:                 map-get($ui-palette, 'orange');
$color-danger:                  map-get($ui-palette, 'red');
$color-disabled:                $color-grey-light;

//  Stand-out
// ===========
$color-standout:                map-get($primary-palette, 'red');
$color-standout-offset:         map-get($primary-palette, 'white');

//  Link
// ======
$color-link:                    $color-white;
$color-link-visited:            $color-white;
$color-link-hover:              $color-white;
$color-link-focus:              $color-white;
$color-link-border-active:      map-get($primary-palette, 'dark-red');

//  Misc
// ======
$color-site-background:         $color-grey-light;
$color-subtle:                  $color-off-white;
$color-border:                  map-get($primary-palette, 'red');
$color-border-input:            map-get($primary-palette, 'red');
$color-border-light:            map-get($primary-palette, 'red');

// ========================================================
