.widget-row--juicer {
}

.juicer-widget {

    .j-loading {
        display: none;
    }

    // scss-lint:disable ImportantRule, NestingDepth
    .juicer-feed {
        font-family: $font-family;

        &.slider {
            .slick-next,
            .slick-prev {
                display: none !important;
            }

            .feed-item:nth-child(2n) {
                .j-text {
                    background-color: darken($color-grey-dark, 8%) !important;
                }
            }

            .j-text {
                background-color: $color-grey-dark !important;
                font-weight: $font-weight-normal !important;
                text-align: left;

                .j-message {
                    &,
                    * {
                        font-size: $normalis;
                    }

                    p {
                        color: $color-white;
                    }

                    a {
                        color: $color-white !important;
                        display: inline;
                        font-weight: $font-weight-semi-bold !important;
                        text-decoration: underline !important;
                        white-space: normal;

                        &:hover {
                            text-decoration: none !important;
                        }

                        &::after {
                            content: none;
                        }
                    }
                }
            }

            .j-meta {
                a {
                    color: $color-white;
                    display: block;

                    &:hover {
                        &::after {
                            height: $border-width-large;
                        }
                    }

                    &::after {
                        height: 0;
                    }
                }
            }

            li.feed-item.words {
                border: 0;

                &::after {
                    content: none;
                }

                .j-poster {
                    display: block !important;
                }

                a {
                    color: $color-white !important;
                }
            }
        }

        h1.referral {
            display: none;
        }

        .j-twitter {
            .j-poster a {
                &::after {
                    height: 0;
                }

                &:first-child {
                    display: none;
                }

                &:hover {
                    &::after {
                        height: $border-width-large;
                    }
                }
            }

            &.feed-item {
                background-color: $color-grey-dark;
            }

            .j-twitter-post-icon {
                &:focus {
                    outline: 0;

                    &::before {
                        box-shadow: inset 0 0 0 $border-width-large $color-standout;
                    }
                }
            }

            .j-image {
                display: none !important;
            }
        }

        .j-twitter-intents {
            a {
                float: left;
                padding-bottom: $space-two-thirds;
            }

            + a {
                float: right;
                padding-bottom: $space-sixth;
            }
        }

        .j-twitter-date {
            margin-top: $border-width-mid;
            top: 0 !important;
        }

        .j-instagram a {
            padding-bottom: $space-third;

            &::before {
                margin-right: 0;
            }
        }

        .j-gallery .j-image::after {
            display: none;
        }
    }

    a.j-image {
        height: 100%;
        padding-bottom: 0;
    }

    a.j-image[tabindex]:focus {
        border: $focus-outline;
        outline: 0;
        z-index: 1;
    }
    // scss-lint:enable ImportantRule, NestingDepth
}
