// Breadcrumb navigation
//
// Renders the list into vertical breadcrumb mode
//
// Markup:
// <ol class="nav nav--breadcrumb">
//   <li><a>Home</a></li>
//   <li><a>Contact</a></li>
//   <li><a>About</a></li>
// </ol>
//
// Styleguide 14.2
$breadcrumb-height:              pxToRem(38px);

.breadcrumb {
    display: inline-block;
    margin-right: $space-and-quarter;
    z-index: 3;

    @include respond-max($small-width) {
        display: none;
    }

    .home-page & {
        @extend %visually-hidden;
    }

    .menu-open & {
        display: none;
    }
}

.breadcrumb__prefix {
    @extend %visually-hidden;
}

.list--breadcrumb {
    align-items: center;
    display: flex;
    height: $breadcrumb-height;
    margin: 0;
    padding: 0;

    .list__item {
        @extend %delta;

        display: inline;
        font-family: $font-family-condensed;
        font-weight: $font-weight-bold;
        margin: 0;
    }

    a {
        @include resetLinks;

        display: inline-block;
        line-height: 1.5;
        padding-bottom: 0;
        text-transform: uppercase;

        &:hover {
            .breadcrumb__link-text {
                &::after {
                    height: $border-width-small;
                }
            }
        }

        &::after {
            content: none;
        }

        .breadcrumb__link-text {
            padding-bottom: $space-quarter;
            position: relative;

            &::after {
                background-color: $color-secondary;
                bottom: 0;
                content: '';
                display: block;
                height: 0;
                left: 0;
                position: absolute;
                transition: height $transition-speed-fast $transition-cubic-bezier;
                width: 100%;
                z-index: -1;
            }
        }
    }

    h1 {
        @extend %delta;

        display: inline;
        font-family: $font-family-condensed;
        font-weight: $font-weight-bold;
        letter-spacing: $letter-spacing-large;
        text-transform: uppercase;

        .page-home & {
            @extend %visually-hidden;
        }
    }
}
