.button--danger,
%button--danger {
    &,
    &:visited {
        background-color: $color-danger;
        border-color: darken($color-danger, 10%);
        color: $color-white;
    }

    &:hover,
    &:active,
    &:focus {
        background-color: lighten($color-danger, 5%);
    }

    &::after {
        content: none;
    }
}
