.error-page {
    align-items: center;
    background-image: url('../images/404.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: $space-quadruple;
    min-height: 40em;
    text-align: center;
}

.page-404 {
    .breadcrumb {
        @include visuallyhidden;
    }

    .contact-callout {
        display: none;
    }

    .site-footer {
        margin-top: 0;
    }
}

// stars

// n is number of stars required
@function multiple-box-shadow ($n) {
    $value: '#{random(3000)}px #{random(3000)}px #FFF';
    @for $i from 2 through $n {
        $value: '#{$value} , #{random(3000)}px #{random(3000)}px #FFF';
    }
    @return unquote($value)
}

$shadows-small:  multiple-box-shadow(700);
$shadows-medium: multiple-box-shadow(200);

@keyframes animStar {
    from {
        transform: translateY(0);
    }

    to {
        transform: translateY(-3000px);
    }
}

.stars {
    bottom: 0;
    left: 0;
    overflow: hidden;
    position: fixed;
    right: 0;
    top: 0;
    z-index: -1;
}

.stars__one {
    animation: animStar 100s linear infinite;
    background: transparent;
    box-shadow: $shadows-small;
    height: $border-width-small;
    width: $border-width-small;

    &::after {
        background: transparent;
        box-shadow: $shadows-small;
        content: '';
        height: $border-width-small;
        left: 0;
        position: absolute;
        top: 3000px;
        width: $border-width-small;
    }
}

.stars__two {
    animation: animStar 150s linear infinite;
    background: transparent;
    box-shadow: $shadows-medium;
    height: $border-width-mid;
    width: $border-width-mid;

    &::after {
        background: transparent;
        box-shadow: $shadows-medium;
        content: '';
        height: $border-width-mid;
        position: absolute;
        top: 3000px;
        width: $border-width-mid;
    }
}
