// Output of the Xinha document editor
.editor {
    // scss-lint:disable IdSelector,SelectorFormat
    .text_align_left {
        text-align: left;
    }

    .text_align_right {
        text-align: right;
    }

    .text_align_center {
        text-align: center;
    }

    .indent {
        margin-left: 20px;
    }

    .indent2 {
        margin-left: 40px;
    }

    strong {
        font-weight: 700;
    }

    em {
        font-style: italic;
    }

    .snippet,
    iframe {
        max-width: 100%;
    }

    .underline_text {
        text-decoration: underline;
    }

    .strikethrough_text {
        text-decoration: line-through;
    }

    .underline_and_strikethrough_text,
    .underline_text.strikethrough_text {
        text-decoration: line-through underline;
    }

    sup,
    .superscript_text {
        vertical-align: super;
    }

    sub,
    .subscript_text {
        vertical-align: sub;
    }

    .float_left {
        float: left;
    }

    .float_right {
        float: right;
    }

    .image_centre {
        display: block;
        margin: $space auto;
        width: 100%;
    }

    td.highlightCell,
    tr.highlightRow td {
        background: #cfc;
    }

    tr.zebra {
        background: #ffc;
    }

    abbr {
        border-bottom: 1px dotted #ccc;
        cursor: help;
    }

    .placeholder {
        background: #eee;
        border: 1px solid #ccc;
        color: #333;
        padding: 2em;
    }

    #editable {
        border: 2px dashed #999;
        padding: 10px;
    }

    #editable::after {
        clear: both;
        content: '.';
        display: block;
        height: 0;
        visibility: hidden;
    }

    #toolbar {
        background-color: #eee;
        border: 1px solid #ddd;
        padding: .5em;
    }

    .imageCaption {
        @extend .image--caption;

        &.float_left {
            margin-left: 0;
            margin-right: $space;

            @include respond-min($small-width) {
                margin-right: $space-and-half;
            }
        }

        &.float_right {
            margin-left: $space;
            margin-right: 0;

            @include respond-min($small-width) {
                margin-left: $space-and-half;
            }
        }

        &.image_centre {
            margin-left: auto;
            margin-right: auto;
        }

        p {
            @extend .image--caption__text;
        }
    }

    img {
        margin: $space;

        @include respond-max($mid-width) {
            width: 100%;
        }

        &.float_right {
            margin-right: 0;
        }

        &.float_left {
            margin-left: 0;
        }
    }

    .button {
        margin-top: $space-two-thirds;
    }

    address {
        font-size: $zeta;
    }

    li {
        a {
            letter-spacing: normal;
            padding-bottom: 0;

            &::after {
                bottom: $border-width-large;
                height: $border-width-small;
            }

            &:hover,
            &:active {
                &::after {
                    height: percentage(1 / 2.8); // visually looks central
                }
            }
        }
    }
    // scss-lint:enable IdSelector,SelectorFormat
}

.editor--columns {
    @include respond-min($mid-width) {
        column-count: 2;
        column-gap: $space;
    }
}
