// ================================================
//  Animation
// ================================================
//   Bounce -- [Example]
//  =====================
@keyframes animation--bounce {
    0%, 100% {
        transform: translateY(0);
    }

    30% {
        transform: translateY(10px);
    }
}

.animation--bounce,
%animation--bounce {
    animation-duration: 3s;
    animation-name: animation--bounce;
}

.animation--infinite,
%animation--infinite {
    animation-iteration-count: infinite;
}

@keyframes fade-in {
    0%   { opacity: 1; }
    100% { opacity: 0; }
}

