.button--standout-filled,
%button--standout-filled {
    &,
    &:visited {
        background-color: $color-standout;
        border: 0;
        transition: background-color $transition-speed-fast $transition-cubic-bezier;

        &::after {
            content: none;
        }
    }

    &:hover,
    &:focus,
    &:active {
        background-color: $color-tertiary;
    }
}
