// =======
//  ICONS
// =======

// Local vars
$icon-size:          $space-and-half + $space-twelfth !default;
$icon-size--inline:  $space-half + $space-twelfth !default;
$icon-size--x-small: $space-two-thirds !default;
$icon-size--small:   $space !default;
$icon-size--mid:     $space-and-quarter !default;
$icon-size--large:   $space-triple !default;
$menu-button-size:   36px;
$close-focus-size:   140%; // focus pseudo needs to sit outside of close icon, this size looks good

.icon {
    background-position: center center;
    background-size: contain;
    display: block;
    height: $icon-size;
    width: $icon-size;

    svg {
        max-height: 100%;
        max-width: 100%;
    }
}

.icon--x-small {
    height: $icon-size--x-small;
    width: $icon-size--x-small;
}

.icon--small {
    height: $icon-size--small;
    width: $icon-size--small;
}

.icon--mid {
    height: $icon-size--mid;
    width: $icon-size--mid;
}

.icon--large {
    height: $icon-size--large;
    width: $icon-size--large;
}

.icon-fallback {
    @extend %visually-hidden;
}

.icon--inline {
    display: inline-block;
    height: $icon-size--inline;
    margin-top: remToEm(-$space-twelfth); // -$space-eighth but as EM
    vertical-align: middle;
    width: $icon-size--inline;

    &:last-child {
        margin-left: $space-third;
    }

    &:first-child {
        margin-right: $space-third;
    }

    &.icon--large {
        height: $icon-size;
        width: $icon-size;
    }
}

.js .icon--embeded {
    width: auto;
}

.icon--full {
    height: 100%;
    width: 100%;
}

.close-icon {
    border: 0;
    float: right;
    height: $menu-button-size;
    padding: 0;
    position: relative;
    transform: rotate(45deg);
    transition: color $transition-speed-fast ease-in-out;
    width: $menu-button-size;
    z-index: 2;

    &,
    &:hover,
    &:focus,
    &:active {
        background-color: transparent;

        .close-icon__line {
            background-color: $color-link-border-active;
        }
    }

    &:focus {
        outline: none;

        &::before {
            border-radius: 100%;
            box-shadow: 0 0 0 $border-width-large $color-secondary;
            content: '';
            height: $close-focus-size;
            left: 50%;
            position: absolute;
            top: 50%;
            transform: translate(-50%, -50%);
            width: $close-focus-size;
        }
    }

    .close-icon__line {
        background-color: $color-secondary;
        display: block;
        height: $border-width-mid;
        margin: ($border-width-mid * 4) auto;
        transition: all $transition-speed ease-in-out;
        width: 100%;

        &:nth-child(1) {
            transform: translateY($menu-button-size / 8);
        }

        &:nth-child(2) {
            transform: translateY(-($menu-button-size) / 8) rotate(90deg);
        }
    }
}
