// Styleguide 18.5
.sidebar {
    @extend %module;

    &__section {
        @extend %boxed;

        margin-bottom: $space;

        &--primary {
        }

        &--secondary {
        }
    }

    &__heading {
        margin-top: 0;
    }

    &__list {
        margin-top: $space-half;

        .list__item {
            margin-top: $space-half;
        }
    }

    &__links {
        margin-top: $space-half;

        .list__item {
            margin-top: $space-half;
        }
    }
}

// Modifiers
@import 'sidebars/primary';
@import 'sidebars/secondary';
