.carousel {
    position: relative;

    .widget__heading + & {
        margin-top: $space-triple + $space-half;
    }
}

.slide {
    display: flex;
    flex-wrap: wrap;
    min-height: 100%;
    position: relative;
    width: 100%;

    .slide__content {
        background-color: $color-white;
        color: $color-primary;
        display: flex;
        flex-direction: column;
        float: left;
        justify-content: center;
        padding: $space-double;
        width: 100%;

        @include respond-min($large-width) {
            width: percentage(2 / 5);
        }

        @include respond-min($xx-large-width) {
            padding: $space-double $space-quadruple;
        }
    }

    .slide__heading {
        @extend %delta;

        color: $color-secondary;
        font-family: $font-family-condensed;
        font-weight: $font-weight-bold;
        letter-spacing: $letter-spacing-large;
        margin-top: 0;
        text-transform: uppercase;
    }

    .slide__text {
        &,
        * {
            @extend %normalis;
        }

        p:first-child {
            margin-top: $space-two-thirds;
        }
    }

    .slide__image {
        align-items: center;
        background-color: $color-off-white;
        display: flex;
        float: left;
        position: relative;
        width: 100%;

        @include respond-min($large-width) {
            width: percentage(3 / 5);
        }
    }

    .slide__img {
        width: 100%;
    }

    .js & {
        .slide__image {
            overflow: hidden;
            position: relative;

            &::after {
                background-color: $color-secondary;
                content: '';
                height: 100%;
                left: 0;
                position: absolute;
                top: 0;
                transition: transform $transition-speed $transition-cubic-bezier;
                transition-delay: $transition-speed-slow;
                width: 100%;
            }
        }

        &.is-selected {
            .slide__image {
                &::after {
                    transform: translateX(-100%);
                }
            }
        }
    }
}

.carousel-arrows {
    bottom: 0;
    position: absolute;
    right: 0;
    z-index: 1;

    .no-js & {
        display: none;
    }
}

.carousel-arrow {
    float: left;
    min-width: $space-quadruple;

    .icon {
        height: $space;
        margin: 0 auto;
        width: $space;
    }
}

.journal-carousel {
    .js & {
        .slide__image {
            &::after {
                content: none;
            }
        }
    }

    .slide {
        .slide__image {
            background-position: center;
            background-size: cover;
            min-height: 36em;
            position: relative;
            width: 100%;

            @include respond-min($mid-width) {
                min-height: 40em;
            }

            &::before {
                background-color: $color-primary;
                content: '';
                height: 100%;
                left: 0;
                opacity: .3;
                position: absolute;
                top: 0;
                width: 100%;
            }

            img {
                position: absolute;
                z-index: -1;
            }
        }

        .slide__content {
            background-color: transparent;
            display: block;
            left: 50%;
            padding: 0;
            position: absolute;
            text-align: center;
            top: 50%;
            transform: translate(-50%, -50%);
            width: 100%;
        }

        .slide__heading {
            @extend %h1;

            color: $color-white;
            margin-bottom: $space-and-half;
            text-transform: none;
        }

        .slide__link {
            font-weight: $font-weight-semi-bold;
        }

        .slide__date {
            @extend %normalis;

            color: $color-white;
            font-weight: $font-weight-normal;
            text-transform: uppercase;
        }
    }

    .carousel-arrows {
        left: 0;
        right: auto;

        @include respond-min($large-width) {
            bottom: 50%;
            left: 0;
            right: auto;
            transform: translateY(50%);
        }
    }

    .carousel-arrows--right {
        left: auto;
        right: 0;
    }
}
