.contact {
    .contact__row {
        @extend %cf;

        margin: $space-triple 0 ($space-triple + $space-half);
    }

    .contact__col {
        @include respond-min($mid-width) {
            float: left;
            padding: 0 $space-double;
            width: 50%;
        }
    }

    .contact__heading {
        @extend %giga;

        margin: 0;
    }

    .contact__text {
        @extend %delta;

        font-weight: $font-weight-bold;
        padding-top: $space-hard;
    }

    .contact__address {
        margin-top: 0;
    }

    .contact__tel {
        @extend %delta;

        letter-spacing: normal;

        &:hover {
            &::after {
                height: $border-width-small;
            }
        }

        &::after {
            height: 0;
        }
    }

    .contact__form-container {
        background-color: $color-white;
        color: $color-primary;
        padding: ($space-quadruple - $space-third) 0;
    }

    .fade-in-delay {
        transform: translateY(var(--translateY));
        transition: transform $transition-speed-fast $transition-cubic-bezier;
        will-change: transform;
    }

    .contact__form-text {
        @extend %delta;
        font-weight: $font-weight-light;
        text-align: center;

        .link {
            color: $color-primary;
            text-transform: none;
        }
    }

    .contact__map-iframe {
        display: block;
        width: 100%;
    }

    .contact__form {
        margin: $space-quadruple 0 0;

        .flickity-viewport {
            width: 100%;
        }

        // scss-lint:disable ImportantRule
        .flickity-slider {
            left: 0 !important;
            transform: translateX(0) !important;
        }

        .hs-form {
            padding-bottom: $space-triple + $space-third;
            position: relative;
        }

        .hs-form-field {
            margin: 0;
            width: 100%;
        }

        .slide {
            left: 0 !important;
            margin: 0;

            .js & {
                opacity: 0;
                transition: all $transition-speed-fast $transition-cubic-bezier;
                visibility: hidden;

                &.is-selected {
                    opacity: 1;
                    visibility: visible;
                }
            }
        }
        // scss-lint:enable ImportantRule

        .form__controls {
            bottom: 0;
            height: $space-triple + $space-third;
            position: absolute;
            width: 100%;
            z-index: 1;

            .no-js & {
                display: none;
            }
        }

        .form__label,
        label {
            @extend %zeta;

            color: $color-primary;
            display: block;
            font-weight: $font-weight-normal;
            margin-top: 0;
            text-align: center;

            &-required {
                color: $color-secondary;
            }
        }

        .input {
            width: 100%;

            &,
            textarea {
                display: block;
            }
        }

        .form__field,
        .hs-input,
        textarea {
            @extend %epsilon;

            background-color: $color-off-white;
            color: $color-primary;
            resize: none;

            @include respond-max($small-width) {
                margin-top: $space-and-two-thirds;
            }

            &:focus {
                box-shadow: inset 0 0 0 $border-width-mid $color-standout;
                outline: $border-width-mid solid $color-standout;
            }
        }

        .form__button {
            margin-top: $space;

            .js & {
                display: none;
            }
        }

        .actions {
            display: none;
        }

        .hs-error-msgs {
            position: absolute;
            top: 0;

            label {
                font-size: $milli;
                letter-spacing: $letter-spacing-large;
                margin-top: $space-quarter;

                @include respond-max($small-width) {
                    font-size: $micro;
                }
            }
        }

        .form__carousel-arrows {
            float: left;
            width: 100%;
        }

        .carousel-arrow {
            font-weight: $font-weight-semi-bold;
            letter-spacing: $letter-spacing-large;
            min-width: auto;
            text-transform: uppercase;
            transition: color $transition-speed-fast $transition-cubic-bezier;

            &:hover,
            &:focus,
            &:active {
                color: $color-tertiary;
            }

            &:focus,
            &:active {
                outline: $focus-outline;
            }
        }

        .carousel-arrow--next {
            float: right;
        }

        input.carousel-arrow--next {
            @extend %normalis;

            color: $color-primary;
            font-style: normal;
            font-weight: $font-weight-semi-bold;
            line-height: unset;
            min-height: unset;
            padding: 0;
            width: auto;
        }

        .carousel-arrow--previous {
            &[disabled] {
                color: $color-grey-light;

                &:focus,
                &:active {
                    outline: 0;
                }
            }
        }

        .form__count {
            float: right;
            font-weight: $font-weight-bold;
            letter-spacing: $letter-spacing-large;
            margin-bottom: $space;
        }

        .form__progress {
            background-color: $color-grey-light;
            height: $border-width-large * 2;
            width: 100%;

            &-bar {
                background-color: $color-secondary;
                display: block;
                height: $border-width-large * 2;
                transition: width $transition-speed-slow;
                width: 0;
            }
        }
    }
}

.contact-callout {
    background-color: $color-standout;
    border: 0;
    padding: $space-triple $space;
    text-align: center;
    transition: background-color $transition-speed-fast $transition-cubic-bezier;
    width: 100%;

    &:hover,
    &:focus,
    &:active {
        background-color: $color-link-border-active;
    }

    &:focus {
        outline: $border-width-large solid $color-secondary;
        position: relative;
        z-index: 1;
    }

    &[data-a11y-toggle]:not([aria-controls]) {
        display: block;

        .no-js & {
            display: none;
        }
    }

    .contact-callout__heading {
        @extend %mega;

        display: block;
        letter-spacing: $letter-spacing-small;
        margin: 0;

        @include respond-max($mid-width) {
            font-size: $kilo;
            line-height: $line-height-kilo;
        }
    }

    .contact-callout__text {
        @extend %delta;

        font-weight: $font-weight-bold;
        letter-spacing: normal;
        margin: 0;
        text-transform: uppercase;

        @include respond-max($mid-width) {
            font-size: $zeta;
            line-height: $line-height-zeta;
        }
    }
}
