// ========================================================
//  Alerts
// ========================================================
// Alerts should be used to provide the user with action
// specific feedback, for example form submission errors.
// The alert block should always contain an alert__heading

// Alert
//
// Base alert class with modifiers
//
// Markup:
// <div class="alert {$modifiers}">
//     <h2 class="alert__heading">Alert Heading</h2>
//     <p class="alert__text">Some alert information</p>
// </div>
//
// .alert--success     - A success alert, useful for positive feedback such as a form submission
// .alert--warning     - A warning alert, useful for warning the user but not necessarily a error
// .alert--danger      - A danger alert, could be used, for example, to list form validation errors


// Local variables
$alertIconSize: $space-double + $space-quarter !default;

.alert {
    background-color: $color-off-white;
    margin: 0 0 $space-double;
    padding: $space-half;
    position: relative;

    @include respond-max($small-width) {
        background-color: $color-off-white;
    }

    @include respond-min($small-width) {
        border-left: $color-off-white solid $space-quadruple;
        padding: ($space - $space-sixth) $space $space;
    }

    &,
    * {
        color: $color-primary;
    }

    .home-page &:first-child {
        margin-top: $space-and-half;
    }


//  Icons
// ======================
    &::before {
        background-size: contain;
        content: '';
        display: inline-block;
        height: $alertIconSize;
        margin: ($space-sixth * -1) $space-half $space-sixth ($space-sixth * -1);
        position: relative;
        vertical-align: middle;
        width: $alertIconSize;

        @include respond-min($small-width) {
            display: block;
            float: none;
            left: ($space-quadruple + $space-double + $space-quarter) / 2 * -1;
            margin: $space-two-thirds 0 0;
            position: absolute;
            top: 0;
        }
    }

    > *:first-child {
        margin-top: 0;
    }
}

.alert__heading {
    @extend %boxed__heading;
    @extend %epsilon;

    margin-bottom: $space-eighth;

    @include respond-max($small-width) {
        display: inline-block;
        margin-bottom: $space-third;
        vertical-align: middle;
        width: calc(100% - #{$alertIconSize + $space-half});
    }
}

.alert__subheading {
    @extend %boxed__heading;

    display: block;
    font-size: $zeta;
    margin: 0;
}

.alert__list {
    clear: both;
    margin-bottom: 0;
    margin-top: $space-half;
}

.alert__text {
    display: inline-block;
    margin-bottom: 0;
    margin-top: $space-half;
}

.alert__heading,
.alert__list,
.alert__text {
    @include respond-max($small-width) {
        &:last-child {
            padding-bottom: $space-half;
        }
    }
}

.alert__form {
    clear: both;

    @include respond-max($small-width) {
        background: $color-white;
        margin: $space-half ($space-half * -1) 0;
        padding: $space-half;
    }
}

.alert__link {
    @extend %zeta;

    text-transform: none;

    &,
    &:visited {
        color: $color-primary;
    }

    &::after {
        bottom: 0;
        height: $border-width-small;
    }

    &:hover,
    &:active {
        &::after {
            height: percentage(1 / 2.2);
        }
    }
}

.alert__heading {
    + * {
        margin-top: 0;
    }
}
