// Widgets
//
// Orbit provides mapping between widget sizes and the susy grid.
// Note, widgets use a custom 100 col grid rather than the default 12 col grid to allow for correct widget sizes.
// You can view an example widget page at /widgets.html to see how they work with the grid.
// Feel free to change as needed (use caution though!).
// See sass/layout/_widgets.scss.
//
// Styleguide 19.5

.widget-row {
    @extend %cf;

    margin-top: $space-triple;

    @include respond-min($mid-width) {
        margin-top: $space * 5.5;
    }

    &:first-of-type {
        margin-top: 0;
    }

    > .container {
        max-width: 1160px;
    }

    .page-home &:first-of-type {
        @include respond-min($large-width) {
            margin-bottom: -($space * 7);
        }
    }
}

.widget__heading {
    font-family: $font-family-condensed;
    letter-spacing: $letter-spacing-large;
    text-transform: uppercase;
}

.fade-in {
    .js & {
        transform: translateY(var(--translateY));
        transition: transform $transition-speed-fast $transition-cubic-bezier;
        will-change: transform;
    }
}

.fade-in-delay {
    .js & {
        opacity: var(--opacity);
        transform: translateY(var(--translateY));
        transition: opacity $transition-speed-fast $transition-cubic-bezier,
                    transform $transition-speed-fast $transition-cubic-bezier;
        will-change: transform;
    }
}

.ease-in {
    .js & {
        opacity: 0;
        transform: translateY($space);
        transition: opacity $transition-speed-slow $transition-cubic-bezier,
                    transform $transition-speed-slow $transition-cubic-bezier;
        transition-delay: $transition-speed-slower;

        &.eased {
            opacity: 1;
            transform: translateY(0);
        }
    }
}

.widget-row--flush {
    margin-top: 0;

    .widget-width {
        margin-bottom: 0;

        + .widget-width {
            @include respond-max($mid-width) {
                margin-top: $space-double;
            }
        }
    }

    + .widget-row--white,
    + .widget-row--grey {
        margin-top: 0;
    }
}

.widget-row--white,
.widget-row--grey {
    .widget__heading {
        color: $color-primary;
    }
}

.widget-row--white {
    + .widget-row--white {
        margin-top: 0;
    }
}

.widget--flush {
    @include respond-min($mid-width) {
        margin-left: -$space;
        margin-right: -$space;
    }
}

.widget-width {
    @include respond-min($mid-width) {
        float: left;
        padding: 0 $space;
        width: 100%;
    }

    @include respond-max($mid-width) {
        margin-bottom: $space;
    }
}

.widget-width--33 {
    .js & {
        transform: translateY(var(--translateY));
        transition: transform $transition-speed-fast $transition-cubic-bezier;
        will-change: transform;
    }

    $transitionVal: .8;

    @for $i from 2 through 3 {
        @include respond-min($mid-width) {
            &:nth-child(#{$i}) {
                transition: transform #{$transitionVal}s $transition-cubic-bezier;
            }
        }

        $transitionVal: $transitionVal * 2;
    }
}

.widget-width--50 {
    .widget-width--100 {
        padding: 0;

        + .widget-width--100 {
            margin-top: $space-and-half;
        }
    }
}

.widget-width--flush-bottom {
    margin-bottom: 0;
}

@include respond-min($mid-width) {
    @include widget-width;
}

.widget-stack {
    .widget:not(:last-child) {
        margin-bottom: $space;
    }
}

.widget {
    @extend %module;

    &__heading {
        position: relative;
        text-align: center;

        &:first-child {
            margin-top: 0;
        }
    }

    &__subheading {
        &:first-child {
            margin-top: 0;
        }
    }

    &__text {
        margin-top: $space-half;
    }

    &__link {
    }

    &__image {
    }

    &__list {
        &__item {
            .widget__list {
                margin-top: $space-half;
            }
        }
    }

    &__form {
    }
}
