// ========================================================
//  Forms
// ========================================================
//  Styling for all forms within the CMS / Xforms,
//  XFP & FormBuilder [XFP2] forms.
// ========================================================
//  Base styling
// ==============
.form,
%form {
    margin: $slug 0 $slug-large;
}

//  Base place-holder of all form elements
// ========================================
//  Define a global form field place-holder for all:
//  inputs, selects, text-areas, radio-buttons &
//  check-boxes
%form__field {
    @extend %delta;

    background-color: transparent;
    border: 0;
    border-bottom: $border-width-small solid $color-white;
    border-radius: 0;
    color: $color-white;
    display: block;
    font-family: $font-family;
    font-style: italic;
    margin-top: $slug-small;
    max-width: 100%;
    padding: $space-half;
    vertical-align: baseline;
    width: 100%;

    @include respond-min($mid-width) {
        font-size: $beta;
        line-height: $line-height-beta;
    }

    &:focus {
        outline: $focus-outline;
    }
}

//  Bordered place-holder
// =======================
//  For use with all elements all, except:
//  check-boxes & radio-buttons
%form__field--bordered {
    background-color: $color-white;
    border: $border-width-small solid $color-border-input;
}

// ========================================================
//  Form Structure
// ================
//  Top level structure, introduction, headings & actions
.form__heading {
    @extend %beta;

    font-weight: $font-weight-bold;
    margin: $space-half 0 $space-double;
}

.form__instructions {

}

.form__questions {

}

.form__section {

}

.form__section-heading {

}

.form__section-content {

}

.form__actions {

}

// ========================================================
//  Form Rows
// ===========
//  Layout for each field block
.form__control {
    margin: $slug-small 0 $slug-large;
    position: relative;
}

.form__component {

}

// ========================================================
//  Form question blocks
// ======================
//  Labels & Legends
.form__label,
.form__legend {
    @extend %delta;

    color: $color-grey-light;
    display: block;
    float: none;
    margin: 0;
    padding: 0;
    position: relative;
    width: 100%;

    @include respond-min($mid-width) {
        font-size: $beta;
        font-weight: $font-weight-light;
        line-height: $line-height-beta;
    }
}

// ========================================================
//  Form Fields
// =============

$form-spacing: $space-half * 2 + $space-twelfth;

.form__field {
    @extend %form__field;

    appearance: none;
    min-height: $epsilon * $line-height-epsilon + $form-spacing;

    @include respond-min($mid-width) {
        min-height: $zeta * $line-height-zeta + $form-spacing;
    }

    @include respond-min($large-width) {
        min-height: $normalis * $line-height-ratio + $form-spacing;
    }
}

.form__field--disabled {
    color: $color-text-disabled;
    font-style: italic;
}

.form__textarea {
    @extend %form__field;
    @extend %form__field--bordered;

    min-height: $space * 5;
    min-height: ($epsilon * $line-height-epsilon * 5) + $form-spacing;
    resize: vertical;

    @include respond-min($mid-width) {
        min-height: ($zeta * $line-height-zeta * 5) + $form-spacing;
    }

    @include respond-min($large-width) {
        min-height: ($normalis * $line-height-ratio * 5) + $form-spacing;
    }
}

.form__select,
%form__select {
    @extend %form__field;

    appearance: none;
    background-position: right $space-half center;
    background-size: $space;
    cursor: pointer;
    min-height: $epsilon * $line-height-epsilon + $form-spacing;
    text-align: left;

    @include respond-min($mid-width) {
        min-height: $zeta * $line-height-zeta + $form-spacing;
    }

    @include respond-min($large-width) {
        min-height: $normalis * $line-height-ratio + $form-spacing;
    }

    &:hover {
        background-color: transparent;
    }
}

.custom-select {
    position: relative;
}

.custom-select__option--value {
    @extend %form__select;

    padding-right: $space-double;

    &:focus {
        background-color: transparent;
    }
}

.custom-select__dropdown {
    left: 0;
    outline: $focus-outline;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;

    .custom-select__option {
        @extend %delta;

        background-color: $color-white;
        border: 0;
        color: $color-secondary;
        cursor: pointer;
        font-style: italic;
        margin: 0;
        padding: $space-third $space-two-thirds;
        text-align: left;
        transition: background-color $transition-speed-fast $transition-cubic-bezier,
                    color $transition-speed-fast $transition-cubic-bezier;
        width: 100%;

        &:hover,
        &:focus {
            background-color: $color-off-white;
            color: $color-primary;
        }

        &:focus {
            outline: $focus-outline;
            position: relative;
            z-index: 1;
        }
    }
}

.form__radio {
    @extend %form__field;
}

$checkboxSize: $normalis-px; // had to use px as using rem altered size depending on label width

.form__checkbox,
input[type=checkbox] {
    appearance: none;
    border: $border-width-mid solid $color-white;
    display: inline-block;
    height: $checkboxSize;
    margin-right: $space-quarter;
    padding: 0;
    width: $checkboxSize;

    &:checked {
        position: relative;

        &::before {
            background-color: $color-white;
            content: '';
            display: block;
            height: $space-fifth;
            left: 55%;
            position: absolute;
            top: 70%;
            transform: rotate(130deg);
            transform-origin: 0 0 0;
            width: $border-width-mid;
        }

        &::after {
            background-color: $color-white;
            content: '';
            display: block;
            height: $border-width-mid;
            left: 35%;
            position: absolute;
            top: 70%;
            transform: rotate(-50deg);
            transform-origin: 0 0 0;
            width: $space-third;
        }
    }
}

// ========================================================
//  Form notes, help & error messaging
// ====================================
.form__required-note {
    color: $color-text-muted;
    display: inline-block;
    font-size: $milli;
    font-style: italic;
    margin-left: $space-half;
}

.form__help {
    @extend %milli;

    color: $color-text-muted;
    display: block;
    margin: $space-two-thirds 0 0;
}

.form__error-indicator {

}

.form__error-indicator-text {

}

.form__error-message {
    // @TODO @extend form__help???
    color: $color-danger;
}


.form--block .form__label--confirmation {
    float: none;
    padding-right: 0;
    width: 100%;
}

.form--block .form__field--confirmation {
    margin-top: $slug-small;
}

// ========================================================
//  Hubspot
// ====================================

.hs-form {
    margin: 0;

    .hs-form-field {
        margin-bottom: $space-third;

        @include respond-max($large-width) {
            margin-top: $space;
        }
    }

    label {
        @extend %delta;

        color: $color-grey-light;
        display: block;
        float: none;
        margin: 0;
        padding: 0;
        position: relative;
        width: 100%;

        @include respond-min($mid-width) {
            font-size: $beta;
            font-weight: $font-weight-light;
            line-height: $line-height-beta;
        }

        @include respond-min($large-width) {
            display: inline;
            margin-right: $space-half;
        }
    }

    .input {
        @include respond-min($large-width) {
            display: inline-block;
            width: $space-quadruple * 4;
        }
    }

    input,
    textarea {
        @extend %form__field;

        appearance: none;
        min-height: $epsilon * $line-height-epsilon + $form-spacing;

        @include respond-min($mid-width) {
            min-height: $zeta * $line-height-zeta + $form-spacing;
        }

        @include respond-min($large-width) {
            margin-top: 0;
            min-height: $normalis * $line-height-ratio + $form-spacing;
        }
    }

    .custom-select__option--value {
        @include respond-min($large-width) {
            margin-top: 0;
        }
    }

    .custom-select__option:disabled {
        display: none;
    }

    .hs-button {
        @extend %button;

        font-style: normal;
        transition: background-color $transition-speed $transition-cubic-bezier,
                    border-color $transition-speed $transition-cubic-bezier;
        width: auto;

        &:hover,
        &:focus {
            background-color: $color-standout;
            border-color: $color-standout;
        }

        &:active {
            background-color: $color-tertiary;
            border-color: $color-tertiary;
        }
    }

    .actions {
        margin-top: $space-double;
    }

    .legal-consent-container {
        margin-top: $space-double;
    }

    .inputs-list {
        list-style: none;
        margin: 0;

        li {
            display: none;

            &:first-child {
                display: block;
            }
        }
    }

    .hs-error-msgs {
        margin-top: $space;

        &,
        * {
            @extend .h6;

            font-weight: $font-weight-semi-bold;
        }
    }

    .hs-fieldtype-booleancheckbox {
        .input {
            width: 100%;
        }

        label {
            @extend %zeta;

            color: $color-white;
            font-weight: $font-weight-normal;
            letter-spacing: unset;
            margin: 0;
        }

        input {
            min-height: unset;
        }
    }

    .hs-richtext {
        @extend p;
    }

    .hs-form-booleancheckbox {
        margin-top: $space-two-thirds;
    }

    .legal-consent-container .field.hs-form-field {
        margin-bottom: 0;
    }
}

.submitted-message {
    margin-top: $space-two-thirds;

    .contact & {
        text-align: center;
    }
}
