$containerLarge: $large-width + remToPx($space-and-two-thirds);

// Container
%container,
.container {
    @extend %cf;

    @include container;

    padding: 0 $space-and-third;
}

%container--flush,
.container--flush {
    padding-left: 0;
    padding-right: 0;
}

.container {
    .container {
        @extend %container--flush;
    }
}

.container--wide {
    max-width: $container-wide;
}

.container--large {
    max-width: $containerLarge;
}

.inner-container-small {
    margin: 0 auto;
    max-width: 620px;
}

.inner-container-mid {
    margin: 0 auto;
    max-width: 800px;
}

.inner-container-large {
    margin: 0 auto;
    max-width: 1050px;
}

.outer-container {
    @extend %container;

    .modular & {
        max-width: 100%;
        padding-left: 0;
        padding-right: 0;
    }
}

.measure {
    max-width: $content-max-width;
}
