%list--social-links,
.list--social-links {
    @extend %list--inline;

    line-height: .8;

    .list__item {
        display: inline-block;
        width: auto;

        &,
        &:last-child {
            margin: 0 ($space-two-thirds + $space-sixth);

            @include respond-max($large-width) {
                margin: 0 $space-half;
            }
        }
    }

    .list__link {
        @include linkUnderlineReset;

        display: block;
        padding: 0;

        &:hover,
        &:focus {
            &::before {
                height: 130%;
                opacity: 1;
                width: 130%; // hover transition ending size, needs to be larger than icon - this spacing looks good
            }
        }

        &::before {
            border: $color-white solid $border-width-large;
            border-radius: 100%;
            content: '';
            height: 10%;
            left: 50%;
            opacity: 0;
            position: absolute;
            top: 50%;
            transform: translate(-50%, -50%);
            transition: $transition-speed-fast $transition-cubic-bezier;
            width: 10%; // hover transition starting size
        }

        &::after {
            content: none;
        }

        &:focus {
            outline: 0;

            &::before {
                outline: $focus-outline;
            }
        }
    }
}
