// Paginator navigation
//
// Pagination style navigation
//
// Markup:
// <nav class="pagination">
//     <ol class="pagination__list">
//         <li classpaginationitem pagination__item--previous"><span class="pagination__link is-disabled">Previous</span></li>
//         <li class="pagination__item pagination__item--next"><a class="pagination__link" href="#">Next</a></li>
//     </ol>
// </nav>
//
// <nav class="pagination pagination--complex">
//     <ol class="pagination__list">
//         <li class="pagination__item pagination__item--previous"><span class="pagination__link is-disabled">Previous</span></li>
//         <li class="pagination__item"><span class="pagination__link is-active">1</span></li>
//         <li class="pagination__item"><a class="pagination__link" href="#">2</a></li>
//         <li class="pagination__item"><a class="pagination__link" href="#">3</a></li>
//         <li class="pagination__item pagination__item--next"><a class="pagination__link" href="#">Next</a></li>
//     </ol>
// </nav>
//
// Styleguide 14.3

$pagination-large-width: 2000px;
$pagination-x-large-width: 2400px;

.pagination {
    background-color: $color-white;
    display: block;
    text-align: center;
}

.pagination__list {
    @extend %cf;
    @extend %list;

    margin-top: 0;

    @include respond-min($small-width) {
        display: flex;
    }
}

.pagination__item {
    display: inline-block;
    margin: 0;
    text-align: left;

    @include respond-min($small-width) {
        float: right;
        width: 50%;
    }
}

.pagination__link {
    display: block;
    height: 100%;
    padding: ($space-triple + $space-half) ($space-double + $space-half);
    position: relative;
    transition: color $transition-speed $transition-cubic-bezier;
    white-space: normal;

    @include respond-min($xxx-large-width) {
        padding: ($space-triple + $space-half) ($space-quadruple * 3);
    }

    @include respond-min($pagination-large-width) {
        padding: ($space-triple + $space-half) ($space-quadruple * 5);
    }

    @include respond-min($pagination-x-large-width) {
        padding: ($space-triple + $space-half) ($space-quadruple * 7);
    }

    &,
    &:visited {
        color: $color-primary;
    }

    &:hover,
    &:focus,
    &:active {
        color: $color-white;

        &::before {
            width: calc(100% + #{$border-width-small});
        }
    }

    &:focus,
    &:active {
        &::before {
            background-color: $color-link-border-active;
        }
    }

    &::after {
        content: none;
    }

    &::before {
        background-color: $color-tertiary;
        content: '';
        height: 100%;
        position: absolute;
        top: 0;
        transition: width $transition-speed $transition-cubic-bezier;
        width: 0;
    }

    &.is-disabled {
        &,
        &:hover,
        &:focus {
            color: $color-grey;
        }

        &::before {
            content: none;
        }
    }
}

.pagination__label {
    @extend %delta;

    display: block;
    font-weight: $font-weight-semi-bold;
    letter-spacing: normal;
    margin-top: 0;
    position: relative;
    text-transform: uppercase;
}

.pagination__meta {
    @extend %normalis;

    display: block;
    font-weight: $font-weight-semi-bold;
    letter-spacing: $letter-spacing-x-large;
    margin-top: $space-and-half;
    position: relative;
    text-transform: uppercase;
}

.pagination__item--previous {
    .pagination__link {
        @include respond-min($mid-width) {
            padding-left: $space * 6;
        }

        &::before {
            right: -$border-width-small;
        }
    }

    .icon-ui-arrow-left {
        left: $space-double;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);

        @include respond-max($mid-width) {
            height: $space-and-fifth;
            left: $space-half;
            width: $space-and-fifth;
        }
    }
}

.pagination__item--next {
    .pagination__link {
        @include respond-min($mid-width) {
            padding-right: $space-quadruple + $space-double;
        }

        &::before {
            left: -$border-width-small;
        }
    }

    .icon-ui-arrow-right {
        position: absolute;
        right: $space-double;
        top: 50%;
        transform: translateY(-50%);
        transition: background $transition-speed $transition-cubic-bezier;

        @include respond-max($mid-width) {
            height: $space-and-fifth;
            right: $space-half;
            width: $space-and-fifth;
        }
    }
}

.pagination__divider {
    background-color: $color-grey-light;
    height: $border-width-small;
    margin: 0;
    width: 100%;

    @include respond-min($small-width) {
        height: auto;
        margin: $space 0;
        width: $border-width-small;
    }
}

// VARS
$pagination-item-max-width: 10rem !default;

.pagination--complex {
    .pagination__item--previous,
    .pagination__item--next {
        max-width: $pagination-item-max-width;

        .pagination__link {
            min-height: $space-double;
        }
    }

    .pagination__label {
        line-height: $space-double;
    }

    .pagination__item--number {
        display: inline-block;
        line-height: $space-double;
        padding: 0;

        @include respond-max($small-width) {
            display: none;
        }

        .pagination__link {
            font-weight: $font-weight-bold;
            padding: 0 $space-third;
        }
    }
}
