// Header
//
// This file is intentially sparse and are intended as placeholder for your header styles
// See sass/layout/_header.scss.
//
// Styleguide 18.2
$logo-height:              30px;
$logo-width:               194px;
$menu-padding-top-large:   pxToRem(284px);
$menu-padding-top-medium:  pxToRem(150px);
$menu-padding-top-small:   pxToRem(90px);
$logo-pointer-size:        pxToRem(400px);
$menu-focus-size:          160%;

.site-header {
    padding: $space-and-third 0;

    @include respond-min($large-width) {
        padding: ($space-double + $space-quarter) 0;
    }

    .menu-opened &,
    .is-fixed & {
        position: absolute;
        width: 100%;
    }

    .container {
        max-width: $full-width;
    }

    .identity__link {
        float: left;
        margin: $border-width-large 0;
        padding: 0;
        transition: opacity $transition-speed $transition-cubic-bezier;
        z-index: 3;

        &::after {
            content: none;
        }

        &:hover {
            opacity: .6;
        }
    }

    .identity__logo {
        height: $logo-height;
        width: $logo-width;
    }

    .nav__controls {
        align-items: center;
        display: flex;
        float: right;
        margin-right: $space-and-half;
    }

    .menu-button-container {
        left: 0;
        pointer-events: none;
        position: fixed;
        top: $space-and-third;
        transform: translate3d(0, 0, 0);
        width: 100%;
        z-index: 3;

        @include respond-min($large-width) {
            top: $space-double + $space-third;
        }

        .container {
            padding: 0 $space-and-third;
        }
    }

    .menu-button {
        border: 0;
        float: right;
        height: $menu-button-size;
        min-width: auto;
        padding: 0;
        pointer-events: auto;
        transition: transform $transition-speed-fast ease-in-out;
        width: $menu-button-size;
        z-index: 3;

        &::before {
            background-color: $color-primary;
            border-radius: 100%;
            content: '';
            height: $menu-focus-size;
            left: 50%;
            position: absolute;
            top: 50%;
            transform: translate(-50%, -50%);
            width: $menu-focus-size;
            z-index: -1;
        }

        &::after {
            content: none;
        }

        &:hover,
        &:focus,
        &:active {
            .menu-button__line {
                background-color: $color-link-border-active;
            }
        }

        &:focus {
            outline: none;

            &::before {
                box-shadow: 0 0 0 $border-width-large $color-secondary;
            }
        }

        .menu-button__line {
            background-color: $color-secondary;
            display: block;
            height: $border-width-mid;
            margin: ($border-width-mid * 4) auto;
            transition: all $transition-speed ease-in-out;
            width: 100%;
        }

        &[aria-expanded="true"] {
            transform: rotate(45deg);
            transition-delay: $transition-speed;

            .menu-button__line {
                &:nth-child(2) {
                    width: 0;
                }

                &:nth-child(1),
                &:nth-child(3) {
                    transition-delay: $transition-speed-fast;
                }

                &:nth-child(1) {
                    transform: translateY($menu-button-size / 4);
                }

                &:nth-child(3) {
                    transform: translateY(-($menu-button-size / 4 + 1)) rotate(90deg);
                }
            }
        }
    }

    .menu-overlay {
        background-color: $color-primary;
        background-image: url('../images/logo-background.svg');
        background-position: center;
        background-repeat: no-repeat;
        height: 100vh;
        left: 0;
        max-height: 100vh;
        padding: $menu-padding-top-medium 0 0;
        position: absolute;
        top: 0;
        width: 100%;
        z-index: 2;

        @include respond-min($xxx-large-width) {
            padding-top: $menu-padding-top-large;
        }

        @include respond-max($mid-width) {
            background-size: 250%;
        }

        .no-js & {
            background-color: transparent;
            background-image: none;
        }
    }

    .menu-overlay__container {
        @extend %container;

        height: 100%;
        overflow-x: hidden;
        overflow-y: auto;
        padding-bottom: $space-double;
    }

    .menu-overlay__col {
        padding: 0 ($space);

        @include respond-min($mid-width) {
            text-align: center;
        }

        @include respond-min($large-width) {
            padding: 0 ($space-double + $space-two-thirds) $space-two-thirds;
        }

        @include respond-min($xx-large-width) {
            float: left;
            text-align: left;
            width: percentage(1 / 2);
        }

        &:first-child {
            display: flex;
            flex-direction: column;
            height: 100%;
            justify-content: space-between;
        }
    }

    .menu-overlay__logo {
        background-color: $color-white;
        background-position: 48% 40%; // visually looks central
        background-size: 60%;
        border-radius: 100%;
        float: right;
        height: $logo-pointer-size;
        width: $logo-pointer-size;

        @include respond-max($xx-large-width) {
            display: none;
        }
    }

    .list--large {
        flex: 0 0 auto;

        .list__link {
            @include respond-max($mid-width) {
                font-size: $kilo;
            }
        }
    }

    .list--slim {
        margin: $space-two-thirds 0;

        .list__link {
            &[data-a11y-toggle]:not([aria-controls]) {
                display: inline-block;
            }
        }
    }

    .nav-footer__link {
        @extend %gamma;

        display: inline-block;
        margin: 0;

        &::after {
            height: $border-width-small;
        }

        &:hover,
        &:focus,
        &:active {
            color: $color-white;
        }

        &:hover {
            &::after {
                height: $border-width-large;
            }
        }

        &:first-child {
            margin: $space $space $space 0;
        }
    }

    .overlay-header & {
        .menu-button {
            &::before {
                background-color: $color-white;
            }
        }
    }
}

.no-js {
    .menu-overlay {
        position: relative;
    }
}

.js {
    .menu-overlay {
        .menu-overlay__col {
            opacity: 0;
            transform: translateY($space-two-thirds);
            transition: transform $transition-speed-slow $transition-cubic-bezier,
                        opacity $transition-speed-slow $transition-cubic-bezier;
        }

        &.is-visible {
            .menu-overlay__col {
                opacity: 1;
                transform: translateY(0);
                transition-delay: $transition-speed-faster;
            }
        }

        &.is-animating {
            display: block;
        }
    }

    .overlay-header .site-header {
        position: absolute;
        top: 0;
        width: 100%;
    }
}
