%list--large,
.list--large {
    .list__item {
        margin-top: $space-quarter;
    }

    .list__link {
        @extend %mega;

        color: $color-grey-light;
        margin: 0;
        transition: color $transition-speed-fast $transition-cubic-bezier;

        &::after {
            height: 0;
        }

        &:hover,
        &:focus,
        &:active {
            color: $color-white;
        }

        &:hover {
            &::after {
                height: $border-width-large;
            }
        }

        &:active {
            &::after {
                height: calc(50% + (#{$space-twelfth} / 2));
            }
        }
    }
}
