%list--tile,
.list--tile {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;

    .list__item {
        margin: $space $space-half 0;

        .js & {
            transform: translateY(var(--translateY));
            transition: transform $transition-speed-fast $transition-cubic-bezier;
            will-change: transform;
        }

        $transitionVal: .6;

        @for $i from 2 through 4 {
            &:nth-child(#{$i}) {
                transition: transform #{$transitionVal}s $transition-cubic-bezier;
            }

            $transitionVal: $transitionVal + .4;
        }
    }

    .list__link {
        @include linkUnderline('.list__link-text', $color-white, transparent);

        align-items: center;
        border: $border-width-large solid $color-white;
        display: block;
        display: flex;
        flex-direction: column;
        height: 100%;
        justify-content: center;
        margin: 0 auto;
        padding: ($space-double + $space-half) $space;
        position: relative;
        text-align: center;
        text-transform: uppercase;
        width: $space * 9;

        &:hover,
        &:focus,
        &:active {
            &::before {
                background-color: $color-tertiary;
                content: '';
                height: 110%; // before element grows bigger than container on hover
                opacity: 1;
                width: 110%;
            }
        }

        &:focus {
            outline: 0;

            &::before {
                outline: $focus-outline;
            }
        }

        &:active {
            &::before {
                background-color: $color-tertiary;
            }
        }

        &::before {
            background-color: transparent;
            bottom: 0;
            content: '';
            display: block;
            height: 100%;
            left: 50%;
            opacity: .3;
            position: absolute;
            right: 0;
            top: 50%;
            transform: translate(-50%, -50%);
            transition: all $transition-speed $transition-cubic-bezier;
            width: 100%;
        }
    }

    .list__link-text {
        flex-grow: 1;
        z-index: 1;
    }

    .icon {
        height: $space-triple;
        margin: 0 auto $space-double;
        transition: background-image $transition-speed $transition-cubic-bezier;
        width: $space-triple;
        z-index: 1;
    }

    .widget-row--white &,
    .widget-row--grey & {
        .list__link {
            border-color: $color-primary;

            &:hover,
            &:focus,
            &:active {
                .list__link-text {
                    color: $color-white;
                }
            }
        }

        .list__link-text {
            color: $color-primary;
            transition: color $transition-speed $transition-cubic-bezier;
        }
    }

    .widget__heading + & {
        margin-top: $space-triple;
    }
}
